import {
  FlatList,
  Image,
  ImageBackground,
  Platform,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  useWindowDimensions,
  View,
} from "react-native";
import React, { useEffect, useState } from "react";
import { BlurView } from "expo-blur";
import AsyncStorage from "@react-native-async-storage/async-storage";
import axios from "axios";
import { Video } from "expo-av";
import { useRoute } from "@react-navigation/native";
import { COLORS, IMAGES } from "../../utills/Utills";
import NavigationBar from "../../components/Common/NavigationBar";
import Banner from "../../components/Common/Banner";
import ImageCarousel from "../../utills/ImageCarousel";
import CustomInputText from "../../components/CustomInputText";
import AntDesign from "react-native-vector-icons/AntDesign";
import { API, ENV } from "../../api/api";
import { DELETE, GET, getToken, POST, PUT } from "../../utills/Helper";
import { LinearGradient } from "expo-linear-gradient";

interface MediaItem {
  files: string;
}

interface PostItem {
  id: number;
  creator: string;
  description: string;
  media: MediaItem[];
}

const CommunityPostList: React.FC = () => {
  const [getPostData, setGetPostData] = useState<PostItem[] | null>([]);
  const [postData, setPostData] = useState<PostItem[] | null>([]);
  const [showComment, setShowComment] = useState<Number | null>();
  const [commentText, setCommentText] = useState<string | null>(null);
  const [commentsData, setcommentsData] = useState<any[] | null>([]);
  const [isEditCommment, setIsEditCommment] = useState<boolean>(false);
  const [editingCommentId, setEditingCommentId] = useState<number | null>(null);
  const [videosReels, setVideosReels] = useState<any | null>([]);

  const route = useRoute<any>();
  const { community_id, backgroundImage } = route.params;
  // console.log(" ~ backgroundImage:", backgroundImage);
  // const community_id = 13;
  console.log("community_id :>> ", community_id);

  const { width: windowWidth } = useWindowDimensions();
  const isSmallScreen = windowWidth < 800;
  const ismobileScreen = windowWidth < 426;

  useEffect(() => {
    GetPost();
  }, []);

  const GetPost = async () => {
    const Token = await getToken();
    const URL = `${API.GET_COMMUNITY_POST + community_id}`;
    try {
      const response = await GET(URL, Token);
      if (response.status === 204 && response.statusText === "No Content") {
        const data = {
          status: 204,
          message: "No posts have been shared in the community yet.",
        };
        setGetPostData(data);
      } else {
        console.log(" ~ GetPost ~ response:", response);
        setGetPostData(response.data.data);
        console.log("response.data.data==>>", response.data.data);
      }
    } catch (error) {
      console.error("Error get post data:", error);
    }
  };

  const handleLikeReaction = async (id: number, reaction: string) => {
    const Token = await getToken();
    const URL = `${API.COMMUNITY_POST_REACTION + id}/`;
    const data = { action: reaction };
    try {
      const response = await POST(URL, data, Token);

      const res = response.data.data;
      if (response.status === 200 || response.status === 201) {
        const updatedPosts = getPostData.map((item) => {
          if (item.id === id) {
            return {
              ...item,
              is_disliked: res.is_disliked,
              is_liked: res.is_liked,
            };
          }
          return item;
        });
        setGetPostData(updatedPosts);
      }
    } catch (error) {
      console.error("res error ===--->>>", error);
    }
  };

  const handleSavePost = async (id: number) => {
    const Token = await getToken();
    const data = { post: id };
    const URL = API.SAVE_COMMUNITY_POST;
    const updatedPosts = await Promise.all(
      getPostData.map(async (item) => {
        if (item.id === id) {
          if (!item.is_saved) {
            // Save the post
            try {
              const response = await POST(URL, data, Token);
              const res = response.data.data;
              if (response.status === 201 && item.id === res.post) {
                return { ...item, is_saved: true };
              }
            } catch (error) {
              console.error("Error saving content for post ID:", id, error);
            }
          } else {
            // Unsave the post
            try {
              const URLRemove = `${API.REMOVE_SAVE_COMMUNITY_POST + id}`;
              const response = await DELETE(URLRemove, Token);
              const res = response.data.data;
              if (response.status === 204) {
                return { ...item, is_saved: false };
              }
            } catch (error) {
              console.error("Error removing content for post ID:", id, error);
            }
          }
        }

        // Return original item if no change
        return item;
      })
    );
    setGetPostData(updatedPosts);
  };

  const getCommment = async (id: number) => {
    const Token = await getToken();
    const URL = `${API.GET_COMMUNITY_POST_COMENT}${id}`;

    try {
      const response = await GET(URL, Token);
      setcommentsData(response.data.data);
    } catch (error) {
      console.error("error get comment :>> ", error);
    }
  };

  const handleComment = (id: number) => {
    if (showComment == id) {
      setcommentsData(null);
      setShowComment(null);
    } else {
      setcommentsData(null);
      getCommment(id);
      setShowComment(id);
      setCommentText(null);
    }
  };

  const handleCommentReaction = async (comment_id: number) => {
    const Token = await getToken();
    const URL = API.COMMUNITY_POST_COMENT_REACTION;
    const data = { comment_id };

    const comment = commentsData.find((item) => item.id === comment_id);
    if (!comment) return; // Optional: handle case where comment isn't found

    if (!comment.is_liked) {
      try {
        const response = await POST(URL, data, Token);
        if (response.status === 201) {
          const updatedPosts = commentsData.map((item) => {
            if (item.id === comment_id) {
              return {
                ...item,
                is_liked: true,
                likes_count: item.likes_count + 1,
              };
            }
            return item;
          });
          setcommentsData(updatedPosts);
        } else {
          console.log("Already liked comment");
        }
      } catch (error) {
        console.error("error comment reaction :>> ", error);
      }
    } else {
      try {
        const URLRemove = `${API.COMMUNITY_POST_COMENT_REACTION_REMOVE}${comment_id}`;
        const response = await DELETE(URLRemove, Token);
        if (response.status === 204) {
          const updatedPosts = commentsData.map((item) => {
            if (item.id === comment_id) {
              return {
                ...item,
                is_liked: false,
                likes_count: Math.max(0, item.likes_count - 1),
              };
            }
            return item;
          });
          setcommentsData(updatedPosts);
        } else {
          console.log("Already unliked comment");
        }
      } catch (error) {
        console.error("error comment reaction :>> ", error);
      }
    }
  };

  const handleCommentCreate = async (id: number) => {
    const Token = await getToken();
    const URL = API.CREATE_COMMUNITY_POST_COMENT;
    const data = { post_id: id, text: commentText };
    if (commentText) {
      setCommentText("");
      try {
        const response = await POST(URL, data, Token);
        const newData = response.data.data;
        if (commentsData && commentsData.length > 0) {
          const filteredComments = commentsData?.filter(
            (item) => item.id !== newData.id
          );
          setcommentsData([newData, ...filteredComments]);
        } else {
          setcommentsData([newData]);
        }
      } catch (error) {
        console.error("error comment create :>> ", error);
      }
    } else {
      console.log("Empty input");
    }
    console.log(" ~ handleCommentCreate ~ data:", data);
  };

  const handleConfirmDeleteComment = (id: number) => {
    if (Platform.OS == "web") {
      const confirmed = window.confirm(
        "Are you sure you want to delete this comment?"
      );
      if (confirmed) {
        handleDeleteComment(id);
      }
    }
  };

  const handleDeleteComment = async (id: number) => {
    const Token = await getToken();
    const URL = `${API.DELETE_COMMUNITY_POST_COMENT}${id}/`;

    try {
      const response = await DELETE(URL, Token);
      const updatedPosts = commentsData.filter((item) => item.id !== id);
      setcommentsData(updatedPosts);
    } catch (error) {
      console.error("error comment reaction :>> ", error);
    }
  };

  const handleEditComment = async (id: number) => {
    setIsEditCommment(true);
    setEditingCommentId(id);
    const findComment = commentsData.find((item) => item.id == id);
    setCommentText(findComment.text);
  };

  const handleCommentUpdate = async () => {
    if (!editingCommentId) return;
    const Token = await getToken();
    const URL = `${API.EDIT_COMMUNITY_POST_COMENT}${editingCommentId}/`;
    const data = { text: commentText };
    setCommentText(null);

    try {
      if (commentText) {
        const response = await PUT(URL, data, Token);
        const updatedComments = commentsData.map((item) => {
          if (item.id === response.data.data.id) {
            return {
              ...item,
              text: response.data.data.text,
            };
          }
          return item;
        });
        setcommentsData(updatedComments);
        // Reset edit mode
        setIsEditCommment(false);
        setEditingCommentId(null);
        setCommentText("");
      } else {
        console.log("Empty field");
      }
    } catch (error) {
      console.error("error comment create :>> ", error);
    }
  };

  //   const videoSection = () => {
  //     const postsWithVideoMedia = [];
  //     const postsWithOtherMedia = [];

  //     getPostData.forEach((post) => {
  //       const videoMedia =
  //         post.media?.filter((m) => m.media_type === "video") || [];
  //       const otherMedia =
  //         post.media?.filter((m) => m.media_type !== "video") || [];

  //       if (videoMedia.length > 0) {
  //         postsWithVideoMedia.push({ ...post, media: videoMedia });
  //       }

  //       if (otherMedia.length > 0) {
  //         postsWithOtherMedia.push({ ...post, media: otherMedia });
  //       }
  //     });
  //     // console.log("postsWithVideoMedia :>> ", postsWithVideoMedia);
  //     // console.log("postsWithOtherMedia :>> ", postsWithOtherMedia);
  //     setPostData(postsWithOtherMedia);
  //     setVideosReels(postsWithVideoMedia);
  //   };

  //   useEffect(() => {
  //     videoSection();
  //   }, [getPostData]);

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={styles.scrollableContentContainer}
      >
        <Banner backNavigation />
        <NavigationBar />
        <View style={{ flex: 1, width: "100%" }}>
          <BlurView intensity={70} style={styles.blurViewContainer}>
            <View style={styles.innerContainer}>
              {getPostData.status == 204 ? (
                <View
                  style={{
                    flex: 1,
                    width: "100%",
                    height: "100%",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <Text
                    style={{
                      color: COLORS.white,
                      width: "100%",
                      textAlign: "center",
                      fontSize: 22,
                    }}
                  >
                    {getPostData.message}
                  </Text>
                </View>
              ) : (
                <FlatList
                  // data={postData}
                  data={getPostData}
                  keyExtractor={(item) => item.id.toString()}
                  style={[
                    styles.scrollContainer,
                    isSmallScreen && styles.scrollContainerSmall,
                  ]}
                  contentContainerStyle={styles.contentContainer}
                  scrollEnabled
                  nestedScrollEnabled
                  renderItem={({ item }) => {
                    // console.log("item :>> ", item);
                    return (
                      <View key={item.id} style={styles.dataItem}>
                        <View
                          style={[
                            styles.itemHeader,
                            isSmallScreen && styles.samallItemHeader,
                          ]}
                        >
                          <Text style={styles.userInfo}>{item.creator}</Text>
                          <View style={styles.shareSaveContainer}>
                            <Image
                              source={IMAGES.share}
                              style={styles.iconLarge}
                            />
                            <TouchableOpacity
                              onPress={() => handleSavePost(item.id)}
                            >
                              <AntDesign
                                name={"star"}
                                color={item.is_saved ? "#EACC6D" : COLORS.white}
                                size={30}
                              />
                            </TouchableOpacity>
                          </View>
                        </View>

                        <View style={styles.itemSubHeader}>
                          <Image
                            source={IMAGES.comment}
                            style={styles.iconTiny}
                          />
                          <Text style={styles.commentText}>Comment</Text>
                        </View>

                        <View style={styles.descriptionWrapper}>
                          <Text
                            style={styles.itemDescription}
                            numberOfLines={2}
                          >
                            {item.description}
                          </Text>
                        </View>

                        <ImageCarousel
                          // images={item.media.map((m) => `${ENV.BASE_URL}${m.files}`)}
                          media={item.media}
                          showDotIndicator={item.media.length > 1}
                        />

                        <View
                          style={[
                            styles.actionsRow,
                            { paddingHorizontal: ismobileScreen ? 0 : 20 },
                          ]}
                        >
                          <View style={{ flex: ismobileScreen ? 0 : 1 }}></View>
                          <TouchableOpacity
                            style={styles.commentButton}
                            onPress={() => handleComment(item.id)}
                          >
                            <Image
                              source={IMAGES.comment}
                              style={styles.iconTiny}
                            />
                            <Text style={{ color: "#fff" }}>Comment</Text>
                          </TouchableOpacity>
                          <View style={styles.likeContainer}>
                            <TouchableOpacity
                              onPress={() =>
                                handleLikeReaction(item.id, "like")
                              }
                            >
                              <AntDesign
                                name={"like1"}
                                color={
                                  item.is_liked ? COLORS.likeBlue : COLORS.white
                                }
                                size={20}
                              />
                            </TouchableOpacity>
                            <TouchableOpacity
                              onPress={() =>
                                handleLikeReaction(item.id, "dislike")
                              }
                            >
                              <AntDesign
                                name={"dislike1"}
                                color={
                                  item.is_disliked
                                    ? COLORS.likeBlue
                                    : COLORS.white
                                }
                                size={20}
                              />
                            </TouchableOpacity>
                          </View>
                        </View>
                        {showComment == item.id && (
                          <View style={styles.commentContainer}>
                            <View style={styles.commentInput}>
                              <View style={styles.commentInputText}>
                                <CustomInputText
                                  placeholder={"Comment"}
                                  placeholderTextColor={COLORS.lightBlack}
                                  value={commentText}
                                  onChangeText={setCommentText}
                                  keyboardType={"ascii-capable"}
                                />
                              </View>
                              <TouchableOpacity
                                onPress={() =>
                                  isEditCommment
                                    ? handleCommentUpdate()
                                    : handleCommentCreate(item.id)
                                }
                                style={styles.commentSubmitBtn}
                              >
                                <Text>
                                  {isEditCommment ? "Update" : "Submit"}
                                </Text>
                              </TouchableOpacity>
                            </View>

                            <View style={styles.commentBorderDivider}></View>

                            <View style={{ gap: 10 }}>
                              <Text style={styles.commentTitle}>Comments</Text>

                              {commentsData && commentsData.length > 0 ? (
                                <FlatList
                                  data={commentsData}
                                  // style={{ maxHeight: 200 }}
                                  contentContainerStyle={styles.flatListContent}
                                  scrollEnabled={false}
                                  nestedScrollEnabled={true}
                                  keyExtractor={(item) => item.id.toString()}
                                  renderItem={(item) => {
                                    return (
                                      <View style={styles.commentBox}>
                                        <Text style={styles.commentUser}>
                                          {item.item.user}
                                        </Text>
                                        <Text style={styles.commentBoxText}>
                                          {item.item.text}
                                        </Text>

                                        <View style={styles.alignCommentIcons}>
                                          <TouchableOpacity
                                            onPress={() => {
                                              handleCommentReaction(
                                                item.item.id
                                              );
                                            }}
                                          >
                                            <AntDesign
                                              name={"like1"}
                                              color={
                                                item.item.is_liked
                                                  ? COLORS.likeBlue
                                                  : COLORS.white
                                              }
                                              size={20}
                                            />
                                          </TouchableOpacity>

                                          <TouchableOpacity
                                            onPress={() => {
                                              handleEditComment(item.item.id);
                                            }}
                                          >
                                            <AntDesign
                                              name={"edit"}
                                              color={COLORS.white}
                                              size={20}
                                            />
                                          </TouchableOpacity>

                                          <TouchableOpacity
                                            onPress={() => {
                                              handleConfirmDeleteComment(
                                                item.item.id
                                              );
                                            }}
                                          >
                                            <AntDesign
                                              name={"delete"}
                                              color={COLORS.white}
                                              size={20}
                                            />
                                          </TouchableOpacity>
                                        </View>
                                      </View>
                                    );
                                  }}
                                  ItemSeparatorComponent={() => (
                                    <View
                                      style={[
                                        styles.commentBorderDivider,
                                        { height: 20, marginBottom: 10 },
                                      ]}
                                    />
                                  )}
                                />
                              ) : (
                                <View>
                                  <Text
                                    style={{
                                      textAlign: "center",
                                      color: COLORS.white,
                                    }}
                                  >
                                    No comments
                                  </Text>
                                </View>
                              )}
                            </View>
                          </View>
                        )}
                      </View>
                    );
                  }}
                />
              )}

              {/* Video Section */}

              {/* {videosReels.length > 0 && (
                  <BlurView
                    intensity={70}
                    style={[
                      styles.sliderContainer,
                      isSmallScreen && styles.smallSliderContainer,
                    ]}
                  >
                    <View style={styles.itemHeader}>
                      <Text style={styles.userInfo}>Videos for you (reels)</Text>
                    </View>
  
                    <FlatList
                      data={videosReels}
                      keyExtractor={(item) => item.id.toString()}
                      scrollEnabled
                      nestedScrollEnabled
                      horizontal
                      renderItem={({ item }) => {
                        // console.log("item :>> ", item);
                        return item.media.map((videoItem, index) => (
                          <View key={index} style={styles.videoWrapper}>
                            <Video
                              source={{
                                uri: `${ENV.BASE_URL}${videoItem.files}`,
                              }}
                              style={styles.video}
                              shouldPlay
                              // resizeMode="cover"
                              isMuted
                            />
                          </View>
                        ));
                      }}
                    />
                  </BlurView>
                )} */}
            </View>
          </BlurView>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};

export default CommunityPostList;

