import {
  View,
  Text,
  StyleSheet,
  Dimensions,
  TouchableOpacity,
  StatusBar,
  ActivityIndicator,
  ScrollView,
  Platform,
  Pressable,
  ImageBackground,
} from "react-native";
import React, { useState, useEffect } from "react";
import { COLORS, FONTS, IMAGES } from "../../utills/Utills";
import { API } from "../../api/api";
import { useNavigation } from "@react-navigation/native";
import AsyncStorage from "@react-native-async-storage/async-storage";
import Banner from "../../components/Common/Banner";
import CustomInput from "../../utills/CustomInput";
import { POST } from "../../utills/Helper";
import { LinearGradient } from "expo-linear-gradient";
const { width } = Dimensions.get("screen");

const emailRegex = /^[a-zA-Z0-9_.+\-]+[\x40][a-zA-Z0-9.\-]+\.[a-zA-Z]{2,}$/;
const passwordRegex =
  /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*(),.?":{}|<>]).{8,}$/;

const Register = () => {
  const navigation = useNavigation<any>();
  const [isLoading, setIsLoading] = useState(false);
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [phoneError, setPhoneError] = useState("");
  const [firstname, setFirstName] = useState("");
  const [firstNameError, setFirstNameError] = useState("");
  const [lastname, setLastName] = useState("");
  const [emailError, setEmailError] = useState("");
  const [password, setPassword] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [isSendOtp, setIsSendOtp] = useState(false);

  useEffect(() => {
    const checkUnVerifiedUser = async () => {
      const isUnVerifiedUser = await AsyncStorage.getItem("unVerified_User");
      if (isUnVerifiedUser) {
        // navigation.navigate("Otp");
      }
    };

    checkUnVerifiedUser();
  }, []);

  const handleFirstName = (text) => {
    setFirstName(text);
    setFirstNameError("");
  };

  const handleEmail = (text) => {
    setEmail(text);
    setEmailError("");
  };

  const handlePhone = (text) => {
    if (/^\d*$/.test(text)) {
      setPhone(text);
      setPhoneError("");
    }
  };

  const handlePassword = (pass) => {
    setPassword(pass);
    setPasswordError("");
  };

  const registerUser = async () => {
    setIsLoading(true);

    try {
      if (firstname == "") {
        setFirstNameError("Enter Your First Name");
        return;
      } else if (email == "") {
        setEmailError("Enter Your Email");
        return;
      } else if (!emailRegex.test(email)) {
        setEmailError("Invalid email");
        return;
      } else if (phone == "") {
        setPhoneError("Enter Your Phone Number");
        return;
      } else if (phone.length !== 10) {
        setPhoneError("Enter only 10 digit Phone Number");
        return;
      } else if (password == "") {
        setPasswordError("Enter Your Password");
        return;
      } else if (!passwordRegex.test(password)) {
        setPasswordError(
          // "Enter password at least 8 characters with one uppercaseletter, one lowercase letter, one digit and one symbol."
          "8+ chars, 1 upper, lower, number & symbol."
        );
        return;
      }

      const data = JSON.stringify({
        email: email,
        password: password,
        phone_number: phone,
        first_name: firstname,
        last_name: lastname,
      });
      const URL = API.SIGN_UP;

      const response = await POST(URL, data);
      console.log(
        "Api Token Register",
        JSON.stringify(response.data.data.access_token)
      );
      const accessToken = response.data.data.access_token;
      // await AsyncStorage.setItem("access_token", accessToken);
      await AsyncStorage.setItem("verify_access_token", accessToken);
      await AsyncStorage.setItem("mainProfile", response.data.data.user_id);

      if (response.data) {
        navigation.navigate("Otp");
      }

      await AsyncStorage.setItem("unVerified_User", data);
    } catch (error) {
      console.log("error resigster:>> ", error);
      if (error.response.data.message.email) {
        console.log("error.response.data.message", error.response.data.message);
        setEmailError(
          error.response.data.message.email ==
            "User with this email address already exists."
            ? "User already registered"
            : ""
        );
      } else if (
        error.response.data.message ==
          "User already created. Verify the OTP to activate your account." &&
        error.response.data.status == 400
      ) {
        setIsSendOtp(true);
        setEmailError(
          "Account already exists. Please verify the OTP to activate it."
        );
      }

      console.error(
        "Registration Error:",
        error.response ? error.response.data : error.message
      );
    } finally {
      setIsLoading(false);
    }
  };

  const handleSendOtp = async () => {
    setIsLoading(true);
    try {
      const data = { contact: email };
      const URL = API.RESEND_OTP;

      const response = await POST(URL, data);
      const accessToken = response.data.data.access_token;
      await AsyncStorage.setItem("verify_access_token", accessToken);

      if (response.data) {
        navigation.navigate("Otp");
      }
    } catch (error) {
      console.log("error login ---", error);
      if (
        error.response.data.message ==
          "Account not found with provided email or phone." &&
        error.response.data.status == 404
      ) {
        setEmailError("Email not found");
      } else if (
        error.response.data.message == "Email or phone number is required." &&
        error.response.data.status == 400
      ) {
        setEmailError("Email is Requied");
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        showsVerticalScrollIndicator={false}
        style={styles.scrollableContent}
      >
        <Banner backNavigation />

        <View style={styles.container}>
          <StatusBar
            animated
            barStyle="light-content"
            backgroundColor="transparent"
            translucent
          />

          <Text style={styles.welcomeText}>Register</Text>
          <Text style={styles.instructionsText}>
            Create your account or login here
          </Text>

          <View style={styles.contentContainer}>
            <CustomInput
              value={firstname}
              onChangeText={(text) => handleFirstName(text)}
              placeholder="Enter Your First Name"
              maxLength={30}
              errorMessage={firstNameError}
            />

            <CustomInput
              value={lastname}
              onChangeText={(text) => setLastName(text)}
              placeholder="Enter Your Last  Name"
              maxLength={30}
            />

            <CustomInput
              value={email}
              onChangeText={(text) => handleEmail(text)}
              placeholder=" Enter Your Email address"
              errorMessage={emailError}
            />

            <CustomInput
              value={phone}
              onChangeText={(text) => handlePhone(text)}
              placeholder=" Enter Phone Number"
              errorMessage={phoneError}
            />

            <CustomInput
              value={password}
              onChangeText={(text) => handlePassword(text)}
              placeholder={"Enter Password"}
              errorMessage={passwordError}
              isPassword
            />

            <TouchableOpacity
              onPress={() => (isSendOtp ? handleSendOtp() : registerUser())}
              activeOpacity={0.8}
              style={styles.signInButton}
              disabled={isLoading}
            >
              {isLoading ? (
                <ActivityIndicator color="black" />
              ) : isSendOtp ? (
                <Text style={styles.signInButtonText}>Send Otp</Text>
              ) : (
                <Text style={styles.signInButtonText}>Register</Text>
              )}
            </TouchableOpacity>
          </View>

          <View style={styles.orContainer}>
            <View style={styles.divider} />
            <Text numberOfLines={1} style={[styles.orText]}>
              Already have an account
            </Text>
            <View style={styles.divider} />
          </View>

          <Pressable
            onPress={() => navigation.navigate("Login")}
            style={styles.signInButton}
          >
            <Text style={styles.signInButtonText}>Already have Account</Text>
          </Pressable>

          <Text style={styles.versionText}>Version: 1.0</Text>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};
const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  container: {
    flex: 1,
    padding: 20,
    alignItems: "center",
  },
  welcomeText: {
    fontWeight: "700",
    fontSize: 30,
    letterSpacing: 0.84,
    color: COLORS.primeryWhite,
    marginTop: Platform.OS === "web" ? 20 : 30,
    fontFamily: FONTS.Playfair,
  },
  instructionsText: {
    fontFamily: FONTS.PoppinsMedium,
    fontSize: 14,
    color: COLORS.primeryWhite,
    marginBottom: 45,
  },
  contentContainer: {
    gap: 10,
    width: "100%",
    alignItems: "center",
  },
  signInButton: {
    width: "100%",
    maxWidth: 436,
    minWidth: 280,
    borderRadius: 46,
    height: 45,
    backgroundColor: COLORS.white,
    justifyContent: "center",
    alignItems: "center",
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 1.2,
    },
    shadowOpacity: 0.17,
    shadowRadius: 1,
    elevation: 5,
  },
  signInButtonText: {
    fontFamily: FONTS.Playfair,
    fontSize: 14,
    fontWeight: "500",
    color: COLORS.black,
  },
  orContainer: {
    flexDirection: "row",
    width: Platform.OS === "web" ? 355 : width - 92,
    justifyContent: "space-between",
    height: 30,
    alignItems: "center",
    paddingVertical: 40,
  },
  divider: {
    height: 1,
    width: 79,
    backgroundColor: COLORS.primarybordercolor,
  },
  orText: {
    fontFamily: FONTS.PoppinsLight,
    fontSize: 14,
    fontWeight: "300",
    color: COLORS.primarygraycolor,
  },
  versionText: {
    color: COLORS.primarygraycolor,
    fontFamily: FONTS.PoppinsRegular,
    fontSize: 13,
    marginTop: 40,
  },
});

export default Register;
