import React, { useState } from "react";
import {
  TextInput,
  View,
  TextInputProps,
  StyleSheet,
  Platform,
  useWindowDimensions,
  Text,
  DimensionValue,
  Pressable,
  TextStyle,
  ViewStyle,
} from "react-native";
import { COLORS, FONTS } from "./Utills";
import Entypo from "react-native-vector-icons/Entypo";

type CustomInputProps = {
  value?: string;
  placeholder?: string;
  placeholderTextColor?: string;
  autoCapitalize?: TextInputProps["autoCapitalize"];
  keyboardType?: TextInputProps["keyboardType"];
  onChangeText?: (text: string) => void;
  maxLength?: number;
  errorMessage?: string;
  numOfColumn?: number;
  isPassword?: boolean;
  label?: string;
  inputStyle?: TextStyle | ViewStyle;
};

const numberOfColumn = (count: number): string => {
  switch (count) {
    case 1:
      return "100%";
    case 2:
      return "48%";
    case 3:
      return "31%";
    case 4:
      return "23%";
    default:
      return "100%";
  }
};

const CustomInput: React.FC<CustomInputProps> = ({
  placeholder,
  placeholderTextColor = COLORS.lightBlack,
  autoCapitalize = "none",
  keyboardType = "ascii-capable",
  value,
  onChangeText,
  errorMessage,
  maxLength,
  numOfColumn = 1,
  isPassword = false,
  label,
  inputStyle,
}) => {
  const [secureText, setSecureText] = useState(isPassword);
  const windowWidth = useWindowDimensions();
  const isLargeScreen = windowWidth.width > 1240;
  const isSmallScreen = windowWidth.width < 800;
  const ismobileScreen = windowWidth.width < 426;

  const calculatedWidth = isLargeScreen
    ? numberOfColumn(numOfColumn)
    : isSmallScreen
    ? "100%"
    : numberOfColumn(numOfColumn > 2 ? numOfColumn - 1 : numOfColumn);

  return (
    <View
      style={[styles.container, { width: calculatedWidth as DimensionValue }]}
    >
      {!!label && (
        <View style={styles.labelView}>
          <Text style={styles.labelText}>{label}</Text>
        </View>
      )}

      <View style={styles.inputWrapper}>
        <TextInput
          autoCapitalize={autoCapitalize}
          keyboardType={keyboardType}
          placeholder={placeholder}
          placeholderTextColor={placeholderTextColor}
          value={value}
          onChangeText={onChangeText}
          maxLength={maxLength}
          style={[
            styles.input,
            inputStyle,
            Platform.OS === "web" && styles.inputWeb,
          ]}
          secureTextEntry={secureText}
          // Add props when needed
        />
        {isPassword && (
          <Pressable
            onPress={() => setSecureText(!secureText)}
            style={styles.passwordToggle}
          >
            <Entypo
              name={secureText ? "eye" : "eye-with-line"}
              size={24}
              color={COLORS.black}
            />
          </Pressable>
        )}
      </View>
      <View style={styles.errContainer}>
        {!!errorMessage && <Text style={styles.errText}>{errorMessage}</Text>}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    alignItems: "center",
  },
  labelView: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    paddingBottom: 10,
    paddingHorizontal: 15,
  },
  labelText: {
    color: COLORS.white,
    fontSize: 16,
  },
  inputWrapper: {
    position: "relative",
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
  },
  input: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    borderRadius: 46,
    height: 45,
    paddingHorizontal: 40,
    fontFamily: FONTS.PoppinsMedium,
    textAlign: "center",
    backgroundColor: COLORS.white,
    ...Platform.select({
      ios: {
        shadowColor: "#000",
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.25,
        shadowRadius: 3.84,
      },
      android: {
        elevation: 5,
      },
      web: {
        boxShadow: "0px 2px 4px rgba(0,0,0,0.25)",
      },
    }),
  },
  inputWeb: {
    // outlineStyle: "none",
  },
  passwordToggle: {
    position: "absolute",
    zIndex: 10,
    right: "4%",
    top: 10,
  },
  errContainer: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    height: 15,
  },
  errText: {
    color: "red",
    fontSize: 12,
  },
});

export default CustomInput;
