import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  StatusBar,
  ActivityIndicator,
  ScrollView,
  ImageBackground,
  Alert,
  Platform,
} from "react-native";
import React, { useState } from "react";

import { COLORS, FONTS, IMAGES } from "../../utills/Utills";
import { API } from "../../api/api";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { RouteProp, useNavigation, useRoute } from "@react-navigation/native";
import Banner from "../../components/Common/Banner";
import CustomInput from "../../utills/CustomInput";
import { POST } from "../../utills/Helper";
import { LinearGradient } from "expo-linear-gradient";

type OtpRouteParams = {
  forgotPassword?: string;
};

const Otp = () => {
  const navigation = useNavigation<any>();
  const [isLoading, setIsLoading] = useState(false);
  const [otp, setOtp] = useState("");
  const [otpError, setOtpError] = useState("");

  const route = useRoute<RouteProp<Record<string, OtpRouteParams>, string>>();
  const isForgotPassScreen = route.params?.forgotPassword;

  const handleOtp = (value) => {
    setOtp(value);
    setOtpError("");
  };

  const handleOtpSubmit = async () => {
    setIsLoading(true);
    const data = { otp };
    const accessToken = await AsyncStorage.getItem("verify_access_token");
    if (!accessToken) {
      console.log("Error", "Access token not found. Please log in again.");
      if (Platform.OS === "web") {
        window.alert(`Alert\n\n Access token not found. Please log in again.`);
      } else {
        Alert.alert("Error", "Access token not found. Please log in again.");
      }
      setIsLoading(false);
      return;
    }
    const URL = API.VERIFIY_OTP;

    try {
      if (otp == "") {
        setOtpError("Enter Otp");
        return;
      }
      const response = await POST(URL, data, accessToken);
      if (response.data.status === 200) {
        await AsyncStorage.setItem("access_token", accessToken);
        await AsyncStorage.removeItem("verify_access_token");
        // navigation.navigate("Home");
        navigation.navigate("MainStack");
      } else {
        throw new Error("Invalid OTP or verification failed.");
      }
      await AsyncStorage.removeItem("unVerified_User");
    } catch (error) {
      if (error.response?.data?.status == 400) {
        setOtpError("Invalid Otp");
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleOtpForgotPass = async () => {
    setIsLoading(true);
    const data = { otp };
    const accessToken = await AsyncStorage.getItem("verify_access_token");
    if (!accessToken) {
      if (Platform.OS === "web") {
        window.alert(`Alert\n\n Access token not found. Please log in again.`);
      } else {
        console.log("Error", "Access token not found. Please log in again.");
      }
      setIsLoading(false);
      return;
    }
    const URL = API.VERIFY_FORGOT_PASSWORD_OTP;

    try {
      if (otp == "") {
        setOtpError("Enter Otp");
        return;
      }
      const response = await POST(URL, data, accessToken);

      if (response.data.status === 200) {
        navigation.navigate("Forgot");
      } else {
        throw new Error("Invalid OTP or verification failed.");
      }
      await AsyncStorage.removeItem("unVerified_User");
    } catch (error) {
      const errorMessage =
        error.response?.data?.message ||
        "Failed to verify OTP. Please try again.";
      Alert.alert("Error", errorMessage);
      if (error.response?.data?.status == 400) {
        setOtpError("Invalid Otp");
      }
    } finally {
      setIsLoading(false); // Hide loader
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
      >
        <Banner backNavigation />

        <View style={styles.container}>
          <StatusBar
            animated
            barStyle="light-content"
            backgroundColor="transparent"
            translucent
          />

          <Text style={styles.welcomeText}>Enter OTP</Text>

          <View style={styles.contentContainer}>
            <CustomInput
              value={otp}
              onChangeText={(text) => handleOtp(text)}
              placeholder="Enter The Otp Sended To Your Email"
              errorMessage={otpError}
            />

            <TouchableOpacity
              onPress={() =>
                isForgotPassScreen ? handleOtpForgotPass() : handleOtpSubmit()
              }
              activeOpacity={0.8}
              style={styles.signInButton}
              disabled={isLoading}
            >
              {isLoading ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={styles.signInButtonText}>Submit OTP</Text>
              )}
            </TouchableOpacity>
          </View>

          <Text style={styles.versionText}>Version: 1.0</Text>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};
const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  container: {
    flex: 1,
    padding: 20,
    alignItems: "center",
    paddingTop: 50,
  },
  welcomeText: {
    fontWeight: "700",
    fontSize: 30,
    letterSpacing: 0.84,
    color: COLORS.primeryWhite,
    fontFamily: FONTS.Playfair,
    paddingVertical: 20,
  },
  contentContainer: {
    gap: 10,
    width: "100%",
    alignItems: "center",
  },
  signInButton: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    borderRadius: 46,
    height: 45,
    backgroundColor: COLORS.white,
    justifyContent: "center",
    alignItems: "center",
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 1.2,
    },
    shadowOpacity: 0.17,
    shadowRadius: 1,
    elevation: 5,
  },
  signInButtonText: {
    fontFamily: FONTS.Playfair,
    fontSize: 14,
    fontWeight: "500",
    color: COLORS.black,
  },
  versionText: {
    color: COLORS.primarygraycolor,
    fontFamily: FONTS.PoppinsRegular,
    fontSize: 13,
    marginTop: 40,
  },
});

export default Otp;
