import React, { useState } from "react";
import {
  StyleSheet,
  ImageBackground,
  ScrollView,
  useWindowDimensions,
  View,
  Text,
  TouchableOpacity,
  ActivityIndicator,
  Platform,
  Alert,
} from "react-native";
import { COLORS, FONTS, IMAGES } from "../../utills/Utills";

import { useNavigation } from "@react-navigation/native";
import Banner from "../../components/Common/Banner";
import NavigationBar from "../../components/Common/NavigationBar";
import CustomInput from "../../utills/CustomInput";
import { GET, POST, getToken } from "../../utills/Helper";
import { API } from "../../api/api";
import { BlurView } from "expo-blur";
import { LinearGradient } from "expo-linear-gradient";

type CommunityDetails = {
  members: string[] | number[];
  name: string;
  description: string;
  category: string;
  allow_member_posts: boolean;
  announcements: string;
};

type CommunityErrors = {
  nameErr: string | null;
  descriptionErr: string | null;
  categoryErr: string | null;
  announcementsErr: string | null;
};

const CreateCommunity = () => {
  const navigation = useNavigation();

  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [communityDetails, setCommunityDetails] = useState<CommunityDetails>({
    members: [],
    name: "",
    description: "",
    category: "",
    allow_member_posts: true,
    announcements: "",
  });

  const [useful_links, setUseful_links] = useState<any>({});
  const [calendar, setCalendar] = useState<any>({});

  const [communityErrors, setCommunityErrors] = useState<CommunityErrors>({
    nameErr: null,
    descriptionErr: null,
    categoryErr: null,
    announcementsErr: null,
  });

  const handleCommunityChange = (
    key: keyof CommunityDetails,
    value: string
  ) => {
    setCommunityDetails((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const setError = (key: keyof CommunityErrors, value: string | null) => {
    setCommunityErrors((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const handleCreateCommunity = async () => {
    setIsLoading(true);
    const accessToken = await getToken();

    if (communityDetails.name == "") {
      setError("nameErr", "Enter Your Community Name");
      setIsLoading(false);
      return;
    } else if (communityDetails.description == "") {
      setError("descriptionErr", "Enter Community Description");
      setIsLoading(false);
      return;
    } else if (communityDetails.category == "") {
      setError("categoryErr", "Enter community Category");
      setIsLoading(false);
      return;
    } else if (communityDetails.announcements == "") {
      setError("announcementsErr", "Enter Announcement");
      setIsLoading(false);
      return;
    }

    const data = {
      ...communityDetails,
      useful_links,
      calendar,
    };

    const URL = API.CREATE_COMMUNITY;

    try {
      const response = await POST(URL, data, accessToken);
      // console.log("response create community ===>>> :>> ", response);
      if (
        response.data.status == 201 &&
        response.data.message == "Community created successfully."
      ) {
        if (Platform.OS === "web") {
          window.alert(
            `Success\n\n${"Your community is successfully created"}`
          );
        } else {
          Alert.alert("Success", "Your community is successfully created");
        }
        navigation.goBack();
      }
    } catch (error) {
      console.log("error craete community :>> ", error);
    } finally {
      setIsLoading(false);
    }
  };

  const windowWidth = useWindowDimensions().width;
  const isSmallScreen = windowWidth < 800;
  const isMobileScreen = windowWidth < 420;
  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={styles.scrollableContentContainer}
      >
        <Banner backNavigation />
        <NavigationBar />

        <BlurView intensity={100} style={styles.container}>
          <Text style={styles.containerHeading}>Create Community</Text>
          <View style={styles.containerInputs}>
            <CustomInput
              value={communityDetails.name}
              onChangeText={(text) => [
                handleCommunityChange("name", text),
                setError("nameErr", null),
              ]}
              placeholder={"Jhon Doe"}
              maxLength={30}
              errorMessage={communityErrors.nameErr}
              numOfColumn={2}
              label={"Community Name"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={communityDetails.description}
              onChangeText={(text) => [
                handleCommunityChange("description", text),
                setError("descriptionErr", null),
              ]}
              placeholder={"This is a test description."}
              errorMessage={communityErrors.descriptionErr}
              numOfColumn={2}
              label={"Community Description"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={communityDetails.category}
              onChangeText={(text) => [
                handleCommunityChange("category", text),
                setError("categoryErr", null),
              ]}
              placeholder={"Technology"}
              errorMessage={communityErrors.categoryErr}
              numOfColumn={2}
              label={"Community Category"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={communityDetails.announcements}
              onChangeText={(text) => [
                handleCommunityChange("announcements", text),
                setError("announcementsErr", null),
              ]}
              placeholder={"Welcome to the community!"}
              errorMessage={communityErrors.announcementsErr}
              numOfColumn={2}
              label={"Community Announcement"}
              inputStyle={styles.inputStyle}
            />
          </View>

          <TouchableOpacity
            onPress={() => handleCreateCommunity()}
            activeOpacity={0.8}
            style={styles.UpdateButton}
            disabled={isLoading}
          >
            {isLoading ? (
              <ActivityIndicator color="#000" />
            ) : (
              <Text style={styles.UpdateButtonText}>Submit</Text>
            )}
          </TouchableOpacity>
        </BlurView>
      </ScrollView>
    </ImageBackground>
  );
};

const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  scrollableContentContainer: {
    alignItems: "center",
  },
  container: {
    flex: 1,
    width: "100%",
    borderWidth: 1,
    borderColor: COLORS.white,
    borderRadius: 16,
    marginTop: 20,
    paddingVertical: 20,
    alignItems: "center",
    gap: 20,
  },

  containerHeading: {
    color: COLORS.white,
    fontSize: 28,
    fontFamily: "PoppinsSemi",
  },

  containerInputs: {
    width: "100%",
    gap: 10,
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "center",
  },
  inputStyle: {
    textAlign: "left",
  },
  UpdateButton: {
    marginTop: Platform.OS === "web" ? 20 : 37,
    width: Platform.OS === "web" ? "30%" : "90%",
    maxWidth: 350,
    minWidth: 180,
    borderRadius: 46,
    height: 45,
    backgroundColor: COLORS.white,
    justifyContent: "center",
    alignItems: "center",
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 1.2,
    },
    shadowOpacity: 0.17,
    shadowRadius: 1,
    elevation: 5,
  },
  UpdateButtonText: {
    fontFamily: FONTS.Playfair,
    fontSize: 14,
    fontWeight: "500",
    color: COLORS.black,
  },
});

export default CreateCommunity;
