import React, { useEffect, useState } from "react";
import {
  StyleSheet,
  ImageBackground,
  ScrollView,
  useWindowDimensions,
  Text,
  View,
  FlatList,
  TouchableOpacity,
  Image,
} from "react-native";
import { COLORS, IMAGES } from "../../utills/Utills";
import { useNavigation, useRoute } from "@react-navigation/native";
import Banner from "../../components/Common/Banner";
import NavigationBar from "../../components/Common/NavigationBar";
import { BlurView } from "expo-blur";
import { GET, getToken } from "../../utills/Helper";
import { API } from "../../api/api";
import { LinearGradient } from "expo-linear-gradient";

interface CommunityItem {
  id: string;
  title: string;
  image: any;
  messageImage: any;
}

const FollowedCommunity = () => {
  const navigation = useNavigation<any>();
  const [communityList, setCommunityList] = useState<any[] | null>([]);

  const windowWidth = useWindowDimensions().width;
  const windowHeight = useWindowDimensions().height;
  const isSmallScreen = windowWidth < 800;
  const isMobileScreen = windowWidth < 420;

  const routes = useRoute<any>().params;
  const route = routes?.screen;

  const handleItemPress = (item: CommunityItem) => {
    console.log("fdkjhsdfh", item);
    navigation.navigate("CommunityDetail", { CommunityId: item.id });
  };

  const handleMyFollowedCommunity = async () => {
    const accessToken = await getToken();
    const URL =
      route === "allCommunitiesList"
        ? API.ALL_COMMUNITY_LIST
        : API.MY_FOLLOWED_COMMUNITY;
    console.log(" ~ handleMyFollowedCommunity ~ URL:", URL);
    try {
      const response = await GET(URL, accessToken);
      if (response.status === 204) {
        setCommunityList(null);
      } else if (response.data.data.length > 0) {
        setCommunityList(response?.data?.data);
      } else {
        setCommunityList(null);
      }
    } catch (error) {
      console.log("error my followed communities :>> ", error);
    }
  };

  useEffect(() => {
    handleMyFollowedCommunity();
  }, []);

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={styles.scrollableContentContainer}
      >
        <Banner backNavigation />
        <NavigationBar />

        <BlurView intensity={100} style={styles.container}>
          <Text style={styles.containerHeading}>
            {route === "allCommunitiesList"
              ? "All Communities "
              : "Following Community"}
          </Text>
          <View
            style={[
              styles.contentContainer,
              { minHeight: windowHeight - 450, maxHeight: windowHeight - 450 },
            ]}
          >
            <View style={{ width: "100%", flex: 1 }}>
              {communityList !== null ? (
                <FlatList
                  data={communityList}
                  keyExtractor={(item, index) => index.toString()}
                  showsVerticalScrollIndicator={false}
                  renderItem={({ item }) => (
                    <TouchableOpacity
                      onPress={() => handleItemPress(item)}
                      style={[styles.card, { marginVertical: 4 }]}
                    >
                      <Text style={styles.searchTxt}>{item?.name}</Text>
                    </TouchableOpacity>
                  )}
                />
              ) : (
                <View style={[styles.card, { marginVertical: 4 }]}>
                  <Text
                    style={[
                      styles.searchTxt,
                      {
                        textAlign: "center",
                        width: "100%",
                      },
                    ]}
                  >
                    Not Found
                  </Text>
                </View>
              )}
            </View>
            {!isSmallScreen && (
              <View
                style={{
                  flex: 1,
                  width: "100%",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Image
                  source={IMAGES.FollowingCommunity}
                  style={{ height: 300, width: 300 }}
                />
              </View>
            )}
          </View>
        </BlurView>
      </ScrollView>
    </ImageBackground>
  );
};

const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  scrollableContentContainer: {
    alignItems: "center",
  },
  container: {
    flex: 1,
    width: "100%",
    borderWidth: 1,
    borderColor: COLORS.white,
    borderRadius: 16,
    marginTop: 20,
    paddingVertical: 20,
    alignItems: "center",
    gap: 20,
  },
  containerHeading: {
    color: COLORS.white,
    fontSize: 28,
    fontFamily: "PoppinsSemi",
  },
  contentContainer: {
    // backgroundColor: "red",
    flexDirection: "row",
    width: "100%",
    // flex: 1,
    paddingHorizontal: 20,
  },
  card: {
    flexDirection: "row",
    alignItems: "center",
    padding: 10,
    backgroundColor: "#2D2B9B",
    marginVertical: 8,
    borderRadius: 10,
    shadowColor: "#000",
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.2,
    shadowRadius: 3,
    elevation: 4,
    gap: 10,
    minWidth: 200,
  },
  searchTxt: {
    fontSize: 20,
    color: "#fff",
    fontFamily: "PoppinsMedium",
  },
});

export default FollowedCommunity;
