import { SafeAreaView, StyleSheet, Text, View } from "react-native";
import Home from "./src/screen/home/Home";
import Route from "./src/navigation/Route";
import { FONTS } from "./src/utills/Utills";
import React, { useEffect } from "react";

export default function App() {
  useEffect(() => {
    async function loadFonts() {
      await FONTS.loadAsync({
        [FONTS.PoppinsRegular]: require("./src/assets/font/poppins.regular.ttf"),
        [FONTS.PoppinsMedium]: require("./src/assets/font/poppins.medium.ttf"),
        [FONTS.Playfair]: require("./src/assets/font/PlayfairDisplay-SemiBold.ttf"),
        [FONTS.PoppinsSemi]: require("./src/assets/font/Poppins-SemiBold.ttf"),
        [FONTS.PoppinsLight]: require("./src/assets/font/Poppins-Light.ttf"),
      });
    }
    loadFonts();
  }, []);
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <Route />
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
});
