import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  StatusBar,
  ActivityIndicator,
  ScrollView,
  ImageBackground,
  Alert,
  Platform,
} from "react-native";
import React, { useState } from "react";
import { COLORS, FONTS, IMAGES } from "../../utills/Utills";
import Banner from "../../components/Common/Banner";
import CustomInput from "../../utills/CustomInput";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { API } from "../../api/api";
import { useNavigation } from "@react-navigation/native";
import { POST } from "../../utills/Helper";
import { LinearGradient } from "expo-linear-gradient";
const passwordRegex =
  /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*(),.?":{}|<>]).{8,}$/;

const Forgot = () => {
  const [isActive, setIsActive] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [newPasswordErr, setNewPasswordErr] = useState("");
  const [confirmPasswordErr, setConfirmPasswordErr] = useState("");

  const navigation = useNavigation<any>();

  const handlePassword = (pass) => {
    setNewPassword(pass.trim());
    setNewPasswordErr("");
    setConfirmPasswordErr("");
  };

  const handleConfirmPassword = (cPass) => {
    setConfirmPassword(cPass.trim());
    setConfirmPasswordErr("");
    setNewPasswordErr("");
  };
  const handleCreateNewPassword = async () => {
    setIsLoading(true);
    const data = {
      new_password: newPassword,
      confirm_new_password: confirmPassword,
    };
    // const accessToken = await AsyncStorage.getItem("access_token");
    const accessToken = await AsyncStorage.getItem("verify_access_token");
    if (!accessToken) {
      if (Platform.OS === "web") {
        window.alert(`Alert\n\nAccess token not found. Please log in again.`);
      } else {
        console.log("Error", "Access token not found. Please log in again.");
      }
      setIsLoading(false);
      return;
    }
    const URL = API.UPDATE_FORGOT_PASSWORD;

    try {
      if (newPassword == "") {
        setNewPasswordErr("Password requied");
        return;
      } else if (!passwordRegex.test(newPassword)) {
        setNewPasswordErr("8+ chars, 1 upper, lower, number & symbol.");
        return;
      } else if (confirmPassword !== newPassword) {
        setConfirmPasswordErr("Password not matched");
        return;
      }
      const response = await POST(URL, data, accessToken);
      if (response.data.status === 200) {
        navigation.navigate("Login");
      }
    } catch (error) {
      if (
        error.response.data.code == "token_not_valid" ||
        error.status == 401
      ) {
        setNewPasswordErr("Token is invalid or expired");
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
      >
        <Banner backNavigation />

        <View style={styles.container}>
          <StatusBar
            animated
            barStyle="light-content"
            backgroundColor="transparent"
            translucent
          />

          <Text style={styles.welcomeText}>Create New Password</Text>

          <View style={styles.contentContainer}>
            <CustomInput
              value={newPassword}
              onChangeText={(text) => handlePassword(text)}
              placeholder="Enter New Password"
              errorMessage={newPasswordErr}
              isPassword
            />

            <CustomInput
              value={confirmPassword}
              onChangeText={(text) => handleConfirmPassword(text)}
              placeholder="Enter Confirm Password"
              errorMessage={confirmPasswordErr}
              isPassword
            />

            <TouchableOpacity
              onPress={() => handleCreateNewPassword()}
              activeOpacity={0.8}
              style={styles.signInButton}
              disabled={isLoading}
            >
              {isLoading ? (
                <ActivityIndicator color="black" />
              ) : (
                <Text style={styles.signInButtonText}>Submit</Text>
              )}
            </TouchableOpacity>
          </View>

          <Text style={styles.versionText}>Version: 1.0</Text>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};
const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  container: {
    flex: 1,
    padding: 20,
    alignItems: "center",
    paddingTop: 50,
  },
  welcomeText: {
    fontWeight: "700",
    fontSize: 30,
    letterSpacing: 0.84,
    color: COLORS.primeryWhite,
    fontFamily: FONTS.Playfair,
    paddingVertical: 20,
  },
  contentContainer: {
    gap: 10,
    width: "100%",
    alignItems: "center",
  },
  signInButton: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    borderRadius: 46,
    height: 45,
    backgroundColor: COLORS.white,
    justifyContent: "center",
    alignItems: "center",
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 1.2,
    },
    shadowOpacity: 0.17,
    shadowRadius: 1,
    elevation: 5,
  },
  signInButtonText: {
    fontFamily: FONTS.Playfair,
    fontSize: 14,
    fontWeight: "500",
    color: COLORS.black,
  },
  versionText: {
    color: COLORS.primarygraycolor,
    fontFamily: FONTS.PoppinsRegular,
    fontSize: 13,
    marginTop: 40,
  },
});

export default Forgot;
