type RouteParams = {
  userData: {
    first_name: string | null;
    last_name: string | null;
    county_domicile: string | null;
    languages_spoken: string | null;
    tarif_per_hour: string | null;
    purpose_of_life: string | null;
    hobbies: string | null;
    user: number | null;
    facebook_url: string | null;
    linkedin_url: string | null;
    twitter_url: string | null;
    instagram_url: string | null;

    // Add other fields you expect in userData
  };
};

type UserDetails = {
  first_name: string;
  last_name: string;
  county_domicile: string;
  languages_spoken: string;
  tarif_per_hour: string;
  purpose_of_life: string;
  hobbies: string;
  user: number;
  facebook_url: string;
  linkedin_url: string;
  twitter_url: string;
  instagram_url: string;
};

type ProfessionalExperience = {
  title: string;
  organization: string;
  duration: string;
  description: string;
  job_role: string;
  job_field: string;
  job_Duration: string;
  job_Description: string;
};

type UserErrors = {
  first_nameErr: string | null;
  last_nameErr: string | null;
  county_domicileErr: string | null;
  hobbiesErr: string | null;
  languages_spokenErr: string | null;
  purpose_of_lifeErr: string | null;
};

type MyImage = {
  uri?: string;
  base64?: string;
  name?: string;
  media_type?: string;
};

const EditProfile = () => {
  const route = useRoute<any>();
  const { userData } = route.params;
  console.log(" ~ EditProfile ~ userData:", userData);

  const [images, setImages] = useState<MyImage>({});

  const [userDetails, setUserDetails] = useState<UserDetails>({
    first_name: userData?.first_name,
    last_name: userData?.last_name,
    county_domicile: userData?.county_domicile,
    languages_spoken: userData?.languages_spoken,
    tarif_per_hour: userData?.tarif_per_hour,
    purpose_of_life: userData?.purpose_of_life,
    hobbies: userData?.hobbies,
    user: userData?.user,
    facebook_url: userData?.facebook_url,
    linkedin_url: userData?.linkedin_url,
    twitter_url: userData?.twitter_url,
    instagram_url: userData?.instagram_url,
  });

  const [professional_experience, setProfessional_experience] = useState<
    ProfessionalExperience[]
  >(
    Array.isArray(userData?.professional_experience) &&
      userData.professional_experience.length > 0
      ? userData.professional_experience
      : [
          {
            title: "",
            organization: "",
            duration: "",
            description: "",
            job_role: "",
            job_field: "",
            job_Duration: "",
            job_Description: "",
          },
        ]
  );

  const [userErrors, setUserErrors] = useState<UserErrors>({
    first_nameErr: null,
    last_nameErr: null,
    county_domicileErr: null,
    languages_spokenErr: null,
    purpose_of_lifeErr: null,
    hobbiesErr: null,
  });
  // const [gender, setGender] = useState("1");

  const [isLoading, setIsLoading] = useState(false);

  const windowWidth = useWindowDimensions().width;
  const isLargeScreen = windowWidth < 1300;
  const isSmallScreen = windowWidth < 800;
  const ismobileScreen = windowWidth < 426;

  const navigation = useNavigation<any>();

  useEffect(() => {}, []);

  const handleUserChange = (key: keyof UserDetails, value: string) => {
    setUserDetails((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  // const handleExperienceChange = (
  //   key: keyof ProfessionalExperience,
  //   value: string
  // ) => {
  //   setProfessional_experience((prev) => ({
  //     ...prev,
  //     [key]: value,
  //   }));
  // };

  const handleExperienceChange = (
    index: number,
    key: keyof ProfessionalExperience,
    value: string
  ) => {
    const updated = [...professional_experience];
    updated[index][key] = value;
    setProfessional_experience(updated);
  };

  const setError = (key: keyof UserErrors, value: string | null) => {
    setUserErrors((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  const addExperience = () => {
    setProfessional_experience((prev) => [
      ...prev,
      {
        title: "",
        organization: "",
        duration: "",
        description: "",
        job_role: "",
        job_field: "",
        job_Duration: "",
        job_Description: "",
      },
    ]);
  };

  const removeExperience = (removeIndex) => {
    const updateData = professional_experience.filter(
      (_, index) => index !== removeIndex
    );
    setProfessional_experience(updateData);
  };

  // const handleUPdateProfile = async () => {
  //   setIsLoading(true);
  //   if (userDetails.first_name == "") {
  //     setError("first_nameErr", "Enter Your Name");
  //     setIsLoading(false);
  //     return;
  //   } else if (userDetails.county_domicile == "") {
  //     setError("county_domicileErr", "Enter Country Domicile");
  //     setIsLoading(false);
  //     return;
  //   } else if (userDetails.languages_spoken == "") {
  //     setError("languages_spokenErr", "Enter Languages Spoken");
  //     setIsLoading(false);
  //     return;
  //   } else if (userDetails.purpose_of_life == "") {
  //     setError("purpose_of_lifeErr", "Purpose of Life");
  //     setIsLoading(false);
  //     return;
  //   } else if (userDetails.hobbies == "") {
  //     setError("hobbiesErr", "Enter Your Hobbies");
  //     setIsLoading(false);
  //     return;
  //   }

  //   const accessToken = await AsyncStorage.getItem("access_token");
  //   console.log(" ~ GetProfile ~ accessToken Home:", accessToken);

  //   if (!accessToken) {
  //     console.error("No access token found in AsyncStorage");
  //     setIsLoading(false);
  //     return;
  //   }

  //   const data = {
  //     first_name: userDetails.first_name,
  //     last_name: userDetails.last_name,
  //     county_domicile: userDetails.county_domicile,
  //     languages_spoken: userDetails.languages_spoken,
  //     tarif_per_hour: userDetails.tarif_per_hour,
  //     purpose_of_life: userDetails.purpose_of_life,
  //     hobbies: userDetails.hobbies,
  //     user: userDetails.user,
  //     professional_experience,
  //   };

  //   const config = {
  //     method: "put",
  //     url: API.EDIT_PROFILE,
  //     headers: {
  //       "Content-Type": "application/json",
  //       Accept: "application/json",
  //       Authorization: `Bearer ${accessToken}`,
  //     },
  //     data,
  //   };

  //   try {
  //     const response = await axios.request(config);
  //     if (response?.data?.message) {
  //       // navigation.navigate("Home");
  //       navigation.navigate("Profile");
  //       console.log("Profile updated successfully");
  //     }
  //   } catch (error) {
  //     console.error("Error Update profile:", error);
  //   } finally {
  //     setIsLoading(false);
  //   }
  // };

  const handleUPdateProfile = async () => {
    setIsLoading(true);

    if (userDetails.first_name == "") {
      setError("first_nameErr", "Enter Your Name");
      setIsLoading(false);
      return;
    } else if (userDetails.county_domicile == "") {
      setError("county_domicileErr", "Enter Country Domicile");
      setIsLoading(false);
      return;
    } else if (userDetails.languages_spoken == "") {
      setError("languages_spokenErr", "Enter Languages Spoken");
      setIsLoading(false);
      return;
    } else if (userDetails.purpose_of_life == "") {
      setError("purpose_of_lifeErr", "Purpose of Life");
      setIsLoading(false);
      return;
    } else if (userDetails.hobbies == "") {
      setError("hobbiesErr", "Enter Your Hobbies");
      setIsLoading(false);
      return;
    }

    const accessToken = await AsyncStorage.getItem("access_token");

    if (!accessToken) {
      console.error("No access token found");
      setIsLoading(false);
      return;
    }

    // const filteredExperience = professional_experience.filter((exp, index) => {

    //   if (index === 0) return true;
    //   return Object.values(exp).some(
    //     (val) => val != null && String(val).trim() !== ""
    //   );
    // });

    const filteredExperience = (() => {
      if (
        professional_experience.length === 1 &&
        Object.values(professional_experience[0]).every(
          (val) => val == null || String(val).trim() === ""
        )
      ) {
        return [];
      }

      return professional_experience.filter((exp, index) => {
        if (index === 0) return true;
        return Object.values(exp).some(
          (val) => val != null && String(val).trim() !== ""
        );
      });
    })();

    console.log("JSON.stringify(filteredExperience) :>> ", filteredExperience);

    const formData = new FormData();

    Object.entries(userDetails).forEach(([key, value]) => {
      formData.append(key, value == null ? "" : String(value));
    });

    if (Platform.OS === "web" && images.uri) {
      const response = await fetch(images.uri);
      const blob = await response.blob();
      formData.append("avatar", blob, images.name);
    }

    formData.append(
      "professional_experience",
      JSON.stringify(filteredExperience)
    );

    const config = {
      method: "put",
      url: API.EDIT_PROFILE,
      headers: {
        "Content-Type": "multipart/form-data",
        Accept: "application/json",
        Authorization: `Bearer ${accessToken}`,
      },
      data: formData,
    };

    try {
      const response = await axios.request(config);
      if (response?.data?.message) {
        navigation.navigate("Profile");
        console.log("Profile updated successfully");
      }
    } catch (error) {
      console.error("Error Update profile:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleImageSelection = async () => {
    const permissionResult =
      await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (permissionResult.granted === false) {
      alert("Permission to access media library is required!");
      return;
    }

    const result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ["images"],
      allowsMultipleSelection: false,
      quality: 1,
      base64: true,
    });

    if (!result.canceled) {
      console.log("result===>>>", result);
      const selectedImages = result.assets.map((asset) => ({
        uri: asset.uri,
        base64: asset.base64,
        name: asset.fileName,
        media_type: "photo",
      }));
      console.log(" ~ selectedImages ~ selectedImages:", selectedImages[0]);

      setImages(selectedImages[0]);
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <StatusBar />
      <ScrollView
        showsVerticalScrollIndicator={false}
        style={{ flex: 1, width: "100%" }}
        contentContainerStyle={{ paddingVertical: 20 }}
      >
        <Banner backNavigation />

        <View style={styles.bodyConatiner}>
          <Text style={styles.welcomeText}>Edit Profile</Text>

          <View style={{ gap: 20 }}>
            <View
              style={{
                width: 150,
                height: 150,
                borderRadius: 200,
                backgroundColor: COLORS.white,
                overflow: "hidden",
              }}
            >
              <Image
                source={{
                  uri: images?.uri
                    ? images?.uri
                    : userData?.avatar
                    ? ENV.BASE_URL + userData?.avatar
                    : "https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png?20150327203541",
                }}
                style={styles.imageStyle}
                resizeMode="cover"
              />
            </View>
            <TouchableOpacity
              onPress={() => handleImageSelection()}
              style={{
                backgroundColor: COLORS.white,
                padding: 10,
                paddingHorizontal: 20,
                borderRadius: 8,
              }}
            >
              <Text style={{ color: COLORS.black }}>Setect image</Text>
            </TouchableOpacity>
          </View>

          <View style={styles.container}>
            <CustomInput
              value={userDetails.first_name}
              onChangeText={(text) => [
                handleUserChange("first_name", text),
                setError("first_nameErr", null),
              ]}
              placeholder={"Enter Your Name"}
              maxLength={30}
              errorMessage={userErrors.first_nameErr}
              numOfColumn={3}
              label={"First Name"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.last_name}
              onChangeText={(text) => handleUserChange("last_name", text)}
              placeholder={"Enter Your Name"}
              maxLength={30}
              numOfColumn={3}
              label={"Last Name"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.county_domicile}
              onChangeText={(text) => [
                handleUserChange("county_domicile", text),
                setError("county_domicileErr", null),
              ]}
              placeholder="Enter Your County Domicile"
              errorMessage={userErrors.county_domicileErr}
              numOfColumn={3}
              label={"County Domicile"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.languages_spoken}
              onChangeText={(text) => [
                handleUserChange("languages_spoken", text),
                setError("languages_spokenErr", null),
              ]}
              placeholder="Enter Languages Spoken"
              errorMessage={userErrors.languages_spokenErr}
              numOfColumn={3}
              label={"Languages Spoken"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.tarif_per_hour}
              onChangeText={(text) => handleUserChange("tarif_per_hour", text)}
              placeholder="Enter Tarif Per Hour"
              numOfColumn={3}
              label={"Tarif"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.purpose_of_life}
              onChangeText={(text) => [
                handleUserChange("purpose_of_life", text),
                setError("purpose_of_lifeErr", null),
              ]}
              placeholder="Purpose of Life"
              errorMessage={userErrors.purpose_of_lifeErr}
              numOfColumn={3}
              label={"Purpose of Life"}
              inputStyle={styles.inputStyle}
            />

            <CustomInput
              value={userDetails.hobbies}
              onChangeText={(text) => [
                handleUserChange("hobbies", text),
                setError("hobbiesErr", null),
              ]}
              placeholder="Hobbies"
              errorMessage={userErrors.hobbiesErr}
              numOfColumn={3}
              label={"Hobbies"}
              inputStyle={styles.inputStyle}
            />
          </View>

          {professional_experience.map((item, index) => (
            <View key={index}>
              <View style={styles.experienceHeading}>
                <Text style={styles.welcomeText}>
                  Professional Experience {index + 1}
                </Text>
                {professional_experience.length > 1 && (
                  <TouchableOpacity
                    onPress={() => removeExperience(index)}
                    style={[styles.addBtn]}
                  >
                    <Entypo name={"minus"} color={COLORS.black} size={35} />
                  </TouchableOpacity>
                )}
              </View>
              <View style={styles.container}>
                <CustomInput
                  value={item.title}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "title", text)
                  }
                  placeholder="Titile"
                  numOfColumn={3}
                  label={"Titile"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.organization}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "organization", text)
                  }
                  placeholder="Organization"
                  numOfColumn={3}
                  label={"Organization"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.duration}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "duration", text)
                  }
                  placeholder="Duration"
                  numOfColumn={3}
                  label={"Duration"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.description}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "description", text)
                  }
                  placeholder="Description"
                  numOfColumn={3}
                  label={"Description"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.job_role}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "job_role", text)
                  }
                  placeholder="Job Role"
                  numOfColumn={3}
                  label={"Job Role"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.job_field}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "job_field", text)
                  }
                  placeholder="Job Field"
                  numOfColumn={3}
                  label={"Job Field"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.job_Duration}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "job_Duration", text)
                  }
                  placeholder="Job Duration"
                  numOfColumn={3}
                  label={"Job Duration"}
                  inputStyle={styles.inputStyle}
                />

                <CustomInput
                  value={item.job_Description}
                  onChangeText={(text) =>
                    handleExperienceChange(index, "job_Description", text)
                  }
                  placeholder="Job Description"
                  numOfColumn={3}
                  // multiline={true}
                  // numberOfLines={isMobileScreen ? 5 : 10}
                  label={"Job Description"}
                  inputStyle={styles.inputStyle}
                />
              </View>
            </View>
          ))}

          <View style={styles.addContainer}>
            <TouchableOpacity onPress={addExperience} style={styles.addBtn}>
              <MaterialIcons name={"add"} color={COLORS.black} size={35} />
            </TouchableOpacity>
          </View>

          <TouchableOpacity
            onPress={handleUPdateProfile}
            activeOpacity={0.8}
            style={styles.UpdateButton}
            disabled={isLoading}
          >
            {isLoading ? (
              <ActivityIndicator color="#000" />
            ) : (
              <Text style={styles.UpdateButtonText}>Update Profile</Text>
            )}
          </TouchableOpacity>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};

