const FILE_ICONS = {
  pdf: require("../assets/images/pdf.png"),
  word: require("../assets/images/word-icon.png"),
  excel: require("../assets/images/excel-icon.png"),
  ppt: require("../assets/images/ppt-icon.png"),
  audio: require("../assets/images/audio-icon.png"),
  video: require("../assets/images/video-icon.jpg"),
  text: require("../assets/images/txt-icon.png"),
  link: require("../assets/images/link-icon.png"),
  default: require("../assets/images/txt-icon.png"),
};

const getFileTypeIcon = (mimeType: string, name: string) => {
  if (mimeType.startsWith("image/")) return null;
  if (mimeType.startsWith("video/")) return FILE_ICONS.video;
  if (mimeType.startsWith("audio/")) return FILE_ICONS.audio;
  if (mimeType.includes("pdf")) return FILE_ICONS.pdf;
  if (mimeType.includes("word")) return FILE_ICONS.word;
  if (mimeType.includes("excel")) return FILE_ICONS.excel;
  if (mimeType.includes("presentation")) return FILE_ICONS.ppt;
  if (mimeType.includes("text")) return FILE_ICONS.text;
  if (mimeType === "text/uri-list" || name.includes("http"))
    return FILE_ICONS.link;
  return FILE_ICONS.default;
};

const PublishPost = () => {
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [titleErr, setTitleErr] = useState("");

  const [files, setFiles] = useState([]);
  const [price, setPrice] = useState<string>("");
  const handleChange = (key) => (text) => {
    // strip non-digits, cap at 3 chars
    const filtered = text.replace(/[^0-9]/g, "").slice(0, 3);
    setPrice((prev) => ({ ...prev, [key]: filtered }));
  };

  const [images, setImages] = useState([]);
  const [uploadImages, setuploadImages] = useState([]);

  const [modalVisible, setModalVisible] = useState(false);
  const [selectedType, setSelectedType] = useState(null);

  const route = useRoute<any>().params;
  const [isFree, setIsFree] = useState(false);
  const [nft, setNft] = useState(false);
  console.log(" ~ isFree:isFree", isFree);

  const handleToggle = (value) => {
    console.log("Toggle value:", value); // true or false
    setIsFree(value);
    if (value == false) {
      setNft(false);
    }
  };
  const handleToggle2 = (value) => {
    console.log("Toggle value:", value); // true or false
    setNft(value);
  };
  const handleSelect = (type) => {
    setSelectedType(type.name);
    let fileTypes = [];

    if (type.name === "Photo") {
      fileTypes = ["image/*"];
    } else if (type.name === "Video") {
      fileTypes = ["video/*"];
    } else if (type.name === "PDF") {
      fileTypes = ["application/pdf"];
    } else if (type.name === "MS files") {
      fileTypes = [
        "application/msword",
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
        "application/vnd.ms-excel",
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        "application/vnd.ms-powerpoint",
        "application/vnd.openxmlformats-officedocument.presentationml.presentation",
      ];
    } else if (type.name === "Word") {
      fileTypes = [
        "application/msword",
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
      ];
    } else if (type.name === "Excel") {
      fileTypes = [
        "application/vnd.ms-excel",
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      ];
    } else if (type.name === "PPT") {
      fileTypes = [
        "application/vnd.ms-powerpoint",
        "application/vnd.openxmlformats-officedocument.presentationml.presentation",
      ];
    } else if (type.name === "Text") {
      fileTypes = ["text/plain"];
    } else if (type.name === "Podcast") {
      fileTypes = ["audio/*"];
    } else if (type.name === "Link") {
      // Maybe open a prompt to enter a URL manually
      console.log("Handle link separately (manual entry)");
      return;
    }

    pickFiles(fileTypes);
  };

  const navigation = useNavigation();
  const { width: windowWidth } = useWindowDimensions();

  const isSmallScreen = windowWidth < 769;
  const isMobileScreen = windowWidth < 426;

  const handlePostTitle = (text) => {
    setTitle(text);
    setTitleErr("");
  };
  useEffect(() => {
    console.log("images-----", images);
  }, [images]);

  const handleImageSelection = async () => {
    // setModalVisible(true);
    // Request permission for the image picker
    const permissionResult =
      await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (permissionResult.granted === false) {
      alert("Permission to access media library is required!");
      return;
    }

    // Launch image picker for multiple images
    const result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ["images", "videos"],
      allowsMultipleSelection: true, // Allows multiple selection
      quality: 5,
      base64: true,
    });

    if (!result.canceled) {
      console.log("result===>>>", result);
      const selectedImages = result.assets.map((asset) => ({
        uri: asset.uri,
        base64: asset.base64,
      }));
      setImages((prevImages) => [...prevImages, ...selectedImages]);
    }
  };

  const pickFiles = async (fileType) => {
    const result = await DocumentPicker.getDocumentAsync({
      multiple: true,
      // type: "*/*", //for all document type
      type: fileType,
      copyToCacheDirectory: false,
    });

    console.log("result picker ==>>", result);

    if (!result.canceled || Array.isArray(result.assets)) {
      const fileList = result.assets;
      console.log("fileList fileList ==> ", fileList);
      const base64Files = fileList.map((file) => {
        const extension = file.name?.split(".").pop()?.toLowerCase();
        const uri = file.uri?.toLowerCase();
        let media_type = "unknown";

        if (uri?.startsWith("http://") || uri?.startsWith("https://")) {
          media_type = "link";
        } else if (!extension) {
          media_type = "unknown";
        } else if (extension === "pdf") {
          media_type = "pdf";
        } else if (["doc", "docx"].includes(extension)) {
          media_type = "word";
        } else if (["xls", "xlsx"].includes(extension)) {
          media_type = "excel";
        } else if (["ppt", "pptx"].includes(extension)) {
          media_type = "ppt";
        } else if (["mp4", "mov", "avi", "mkv"].includes(extension)) {
          media_type = "video";
        } else if (
          ["jpg", "jpeg", "png", "gif", "bmp", "webp", "heic"].includes(
            extension
          )
        ) {
          media_type = "photo";
        } else if (["txt", "md", "csv"].includes(extension)) {
          media_type = "text";
        } else if (["mp3", "m4a", "aac", "wav", "ogg"].includes(extension)) {
          media_type = "podcast";
        }
        return {
          name: file.name,
          mimeType: file.mimeType,
          uri: file.uri,
          media_type: media_type,
        };
      });

      console.log("base64Files ==. ", base64Files);

      setFiles((prev) => [...prev, ...base64Files]);
    }
  };

  const handlePost = async () => {
    if (title == "") {
      setTitleErr("Title is required");
      return;
    } else if (files.length == 0) {
      setTitleErr("Image is required");
      return;
    }

    const Token = await getToken();

    try {
      const formData = new FormData();
      // Append the fields to the FormData object
      formData.append("title", title);

      if (route?.screen === "communityPost") {
        formData.append("community", route?.community_id);
        formData.append("content", description);
        formData.append("is_inappropriate", "false");
      } else {
        formData.append("description", description);
      }
      formData.append("is_free", !isFree);
      formData.append("price", price);
      formData.append("nft", nft);
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        console.log(" ~ handlePost ~ file:", file);

        if (Platform.OS === "web") {
          const response = await fetch(file.uri);
          const blob = await response.blob();
          console.log(" ~ handlePost ~ response:", response);
          console.log(" ~ handlePost ~ blob:", blob);
          formData.append("media", blob, file.name);
          formData.append("media_type", file.media_type);
        }
      }

      const config = {
        method: "post",
        url:
          route?.screen === "communityPost"
            ? API.CREATE_COMMUNITY_POST
            : API.UPLOAD_CONTENT,
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
        },
        data: formData,
      };

      const response = await axios.request(config);
      console.log("Api Responce Post data", response.data);

      if (response.data) {
        console.log("User post data successfully");
        navigation.goBack();
      }
    } catch (error) {
      console.error(
        "Post Error:",
        error.response ? error.response.data : error.message
      );
    } finally {
      //   setIsLoading(false);
    }
  };
  return (
    <SafeAreaView style={styles.container}>
      <StatusBar />

      <ImageBackground style={styles.backgroundImage} resizeMode="cover">
        <LinearGradient
          colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
          start={{ x: 0, y: 0 }}
          end={{ x: 1, y: 1 }}
          style={StyleSheet.absoluteFill}
        />
        <BlurView
          intensity={100}
          style={[
            styles.postContainer,
            isSmallScreen && styles.smallPostContainer,
            isMobileScreen && styles.mobilePostContainer,
          ]}
        >
          <Text style={styles.modalTitle}>
            {route?.screen === "communityPost"
              ? "Create Community Post"
              : "Publish Something"}
          </Text>

          <View style={styles.fileView}>
            {/* {images.length == 0 ? ( */}
            {files.length == 0 ? (
              <TouchableOpacity
                style={{
                  justifyContent: "center",
                  alignItems: "center",
                  borderWidth: 1,
                  borderColor: COLORS.darkbordercolor,
                  padding: 10,
                  borderRadius: 8,
                  borderStyle: "dashed",
                }}
                // onPress={() => handleImageSelection()}
                onPress={() => setModalVisible(true)}
              >
                <Entypo name={"upload-to-cloud"} size={70} />
                <Text>Upload Image</Text>
              </TouchableOpacity>
            ) : (
              <>
                <TouchableOpacity
                  // onPress={() => handleImageSelection()}
                  onPress={() => setModalVisible(true)}
                  style={styles.imgPickerBtn}
                >
                  <Text style={styles.imgPickerTxt}>Upload Image</Text>
                </TouchableOpacity>

                <ScrollView
                  style={styles.flatListContainer}
                  horizontal={true}
                  indicatorStyle="black"
                >
                  {/* {images.map((item, index) => (
                    <View key={index} style={styles.imgView}>
                      <Image
                        source={{ uri: item.uri }}
                        style={styles.imageStyle}
                        resizeMode="contain"
                      />
                    </View>
                  ))} */}

                  {files.map((file, index) => {
                    // console.log("files ==<<<", file);
                    const icon = getFileTypeIcon(file.mimeType, file.name);

                    return (
                      <View key={index} style={styles.fileItem}>
                        {icon ? (
                          <Image source={icon} style={styles.icon} />
                        ) : (
                          <Image
                            source={{ uri: file.uri }}
                            style={styles.thumbnail}
                          />
                        )}
                      </View>
                    );
                  })}
                </ScrollView>
              </>
            )}
          </View>

          <View style={styles.titleView}>
            <Text style={styles.titleText}>Add Title</Text>
            <CustomInputText
              inputStyle={{}}
              placeholder={"Title"}
              placeholderTextColor={COLORS.lightBlack}
              value={title}
              onChangeText={(text) => handlePostTitle(text)}
              keyboardType={"ascii-capable"}
              error={titleErr}
            />
            <Text style={styles.titleText}>
              {route?.screen === "communityPost"
                ? "Add Content"
                : "Add Description"}
            </Text>
            <CustomInputText
              inputStyle={{ height: 80 }}
              placeholder={
                route?.screen === "communityPost" ? "Content" : "Description"
              }
              placeholderTextColor={COLORS.lightBlack}
              value={description}
              onChangeText={(text) => setDescription(text)}
              keyboardType={"ascii-capable"}
              // multiline={true}
              // numberOfLines={5}
            />
            <View
              style={{ flexDirection: "row", justifyContent: "space-between" }}
            >
              <View>
                <Text style={styles.titleText}>{"Paid"}</Text>
                <CustomToggle onToggle={handleToggle} initialValue={isFree} />
              </View>
              {isFree && (
                <View>
                  <Text style={styles.titleText}>{"Enter Price"}</Text>
                  <TextInput
                    value={price}
                    onChangeText={(text) => {
                      const onlyNumbers = text.replace(/[^0-9]/g, "");
                      setPrice(onlyNumbers);
                    }}
                    keyboardType="numeric"
                    maxLength={3}
                    style={{
                      height: 30,
                      width: 100,
                      backgroundColor: COLORS.white,
                      borderRadius: 10,
                      padding: 10,
                      textAlign: "center",
                    }}
                  />
                </View>
              )}
            </View>
            {isFree && <Text style={styles.titleText}>{"NFT"}</Text>}
            {isFree && (
              <CustomToggle onToggle={handleToggle2} initialValue={nft} />
            )}
          </View>

          <View style={styles.bottomBar}>
            <TouchableOpacity
              style={styles.postBtn}
              onPress={() => navigation.goBack()}
            >
              <Text style={styles.postButtonText}>Cancel</Text>
            </TouchableOpacity>
            <TouchableOpacity
              style={styles.postBtn}
              onPress={() => handlePost()}
            >
              <Text style={styles.postButtonText}>Post</Text>
            </TouchableOpacity>
          </View>
        </BlurView>

        <FileTypeModal
          visible={modalVisible}
          onClose={() => setModalVisible(false)}
          onSelect={handleSelect}
          route={route?.screen}
        />
      </ImageBackground>
    </SafeAreaView>
  );
};

