import {
  View,
  Text,
  StyleSheet,
  Dimensions,
  TouchableOpacity,
  StatusBar,
  ActivityIndicator,
  ScrollView,
  Platform,
  Pressable,
  ImageBackground,
} from "react-native";
import React, { useState } from "react";

import { COLORS, FONTS, IMAGES } from "../../utills/Utills";
import { API } from "../../api/api";
import AsyncStorage from "@react-native-async-storage/async-storage";
import Banner from "../../components/Common/Banner";
import CustomInput from "../../utills/CustomInput";
import { POST } from "../../utills/Helper";
import { LinearGradient } from "expo-linear-gradient";
const { width } = Dimensions.get("screen");

const emailRegex = /^[a-zA-Z0-9_.+\-]+[\x40][a-zA-Z0-9.\-]+\.[a-zA-Z]{2,}$/;

const Login = ({ navigation }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingForgotPass, setIsLoadingForgotPass] = useState(false);
  // const [email, setEmail] = useState("admin@gmail.com");
  const [email, setEmail] = useState("test@yopmail.com");
  const [emailError, setEmailError] = useState("");
  // const [password, setPassword] = useState("Admin@123");
  const [password, setPassword] = useState("Password@1234");
  const [passwordError, setPasswordError] = useState("");
  const [isForgotPass, setIsForgotPass] = useState(true);

  const handleEmail = (text) => {
    setEmail(text);
    setEmailError("");
    setPasswordError("");
  };

  const handlePassword = (pass) => {
    setPassword(pass);
    setPasswordError("");
  };

  const handleLogin = async () => {
    setIsLoading(true);
    const data = {
      email,
      password,
    };
    const URL = API.LOGIN;

    try {
      if (email == "") {
        setEmailError("Enter Your Email");
        return;
      } else if (!emailRegex.test(email)) {
        setEmailError("Invalid email");
        return;
      } else if (password == "") {
        setPasswordError("Enter Your Password");
        return;
      }
      const response = await POST(URL, data);
      console.log("Api Token Login", response.data);
      const accessToken = response.data.data.access_token;

      await AsyncStorage.setItem("access_token", accessToken);
      await AsyncStorage.setItem("mainProfile", response.data.data.user_id);

      if (response.data) {
        console.log("User registered successfully");
        // navigation.navigate("Home");
        navigation.navigate("MainStack");
      }
    } catch (error) {
      console.log("error login ---", error);
      if (
        error.response.data.message ==
        "You are not verified, Please verify your account first."
      ) {
        console.log("it is not verfied account");
        setIsForgotPass(false);
      }
      if (error.response.data) {
        setPasswordError(error.response.data?.message);
      }
      console.error(
        "Registration Error:",
        error.response ? error.response.data : error.message
      );
    } finally {
      setIsLoading(false);
    }
  };

  const handleForgotPassword = async () => {
    setIsLoadingForgotPass(true);

    try {
      const data = { contact: email };
      const URL = API.FORGOT_PASSWORD_OTP;

      const response = await POST(URL, data);
      const accessToken = response.data.data.reset_token;
      await AsyncStorage.setItem("verify_access_token", accessToken);
      await AsyncStorage.setItem("mainProfile", response.data.data.user_id);
      if (response.data) {
        navigation.navigate("Otp", { forgotPassword: true });
      }
    } catch (error) {
      console.log("error forgot otp api ---", error);
      if (
        error.response.data.message ==
          "Account not found with provided email or phone." &&
        error.response.data.status == 404
      ) {
        setEmailError("Email not found");
      } else if (
        error.response.data.message == "Email or phone number is required." &&
        error.response.data.status == 400
      ) {
        setEmailError("Email is Requied");
      }
    } finally {
      setIsLoadingForgotPass(false);
    }
  };

  const handleVerfiyAccount = async () => {
    setIsLoadingForgotPass(true);
    try {
      const data = { contact: email };
      const URL = API.RESEND_OTP;

      const response = await POST(URL, data);
      const accessToken = response.data.data.access_token;
      await AsyncStorage.setItem("verify_access_token", accessToken);

      if (response.data) {
        navigation.navigate("Otp");
      }
    } catch (error) {
      console.log("error verify otp api ---", error);
      if (
        error.response.data.message ==
          "Account not found with provided email or phone." &&
        error.response.data.status == 404
      ) {
        setEmailError("Email not found");
      } else if (
        error.response.data.message == "Email or phone number is required." &&
        error.response.data.status == 400
      ) {
        setEmailError("Email is Requied");
      }
    } finally {
      setIsLoadingForgotPass(false);
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
      >
        <Banner />

        <View style={styles.container}>
          <StatusBar
            animated
            barStyle="light-content"
            backgroundColor="transparent"
            translucent
          />

          <Text style={styles.welcomeText}>SignIn</Text>
          <Text style={styles.instructionsText}>
            Welcome back to your account
          </Text>

          <View style={styles.contentContainer}>
            <CustomInput
              value={email}
              onChangeText={(text) => handleEmail(text)}
              placeholder={"Email address"}
              errorMessage={emailError}
            />

            <CustomInput
              value={password}
              onChangeText={(text) => handlePassword(text)}
              placeholder={"Password"}
              errorMessage={passwordError}
              isPassword
            />

            <TouchableOpacity
              onPress={() =>
                isForgotPass ? handleForgotPassword() : handleVerfiyAccount()
              }
              style={styles.forgotPassword}
              disabled={isLoadingForgotPass}
            >
              {isLoadingForgotPass ? (
                <ActivityIndicator color="white" />
              ) : (
                <Text style={[styles.forgotPasswordText]}>
                  {isForgotPass ? "Forgot your password ?" : "Verify Account ?"}
                </Text>
              )}
            </TouchableOpacity>

            <TouchableOpacity
              onPress={handleLogin}
              activeOpacity={0.8}
              style={styles.signInButton}
              disabled={isLoading}
            >
              {isLoading ? (
                <ActivityIndicator color="black" />
              ) : (
                <Text style={styles.signInButtonText}>SignIn</Text>
              )}
            </TouchableOpacity>
          </View>

          <View style={styles.orContainer}>
            <View style={styles.divider} />
            <Text numberOfLines={1} style={[styles.orText]}>
              Don't have an account
            </Text>
            <View style={styles.divider} />
          </View>

          <Pressable
            onPress={() => navigation.navigate("Register")}
            style={styles.signInButton}
          >
            <Text style={styles.signInButtonText}>Register New Acoount</Text>
          </Pressable>

          <Text style={styles.versionText}>Version: 1.0</Text>
        </View>
      </ScrollView>
    </ImageBackground>
  );
};
const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    paddingHorizontal: 20,
  },
  scrollableContent: {
    flex: 1,
    paddingVertical: 20,
  },
  container: {
    flex: 1,
    alignItems: "center",
  },
  welcomeText: {
    fontWeight: "700",
    fontSize: 30,
    letterSpacing: 0.84,
    color: COLORS.primeryWhite,
    marginTop: Platform.OS === "web" ? 20 : 30,
    fontFamily: FONTS.Playfair,
  },
  instructionsText: {
    fontFamily: FONTS.PoppinsMedium,
    fontSize: 14,
    color: COLORS.primeryWhite,
    marginBottom: 45,
  },
  contentContainer: {
    gap: 10,
    width: "100%",
    alignItems: "center",
  },
  forgotPassword: {
    width: 426,
    minWidth: 250,
  },
  forgotPasswordText: {
    textDecorationLine: "underline",
    fontFamily: FONTS.PoppinsLight,
    fontWeight: "500",
    fontSize: 14,
    color: COLORS.white,
    textAlign: "right",
  },
  signInButton: {
    width: "100%",
    maxWidth: 436,
    minWidth: 250,
    borderRadius: 46,
    height: 45,
    backgroundColor: COLORS.white,
    justifyContent: "center",
    alignItems: "center",
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 1.2,
    },
    shadowOpacity: 0.17,
    shadowRadius: 1,
    elevation: 5,
  },
  signInButtonText: {
    fontFamily: FONTS.Playfair,
    fontSize: 14,
    fontWeight: "500",
    color: COLORS.black,
  },
  orContainer: {
    flexDirection: "row",
    width: Platform.OS === "web" ? 345 : width - 92,
    justifyContent: "space-between",
    height: 30,
    alignItems: "center",
    paddingVertical: 40,
  },
  divider: {
    height: 1,
    width: 79,
    backgroundColor: COLORS.primarybordercolor,
  },
  orText: {
    fontFamily: FONTS.PoppinsLight,
    fontSize: 14,
    fontWeight: "300",
    color: COLORS.primarygraycolor,
  },
  versionText: {
    color: COLORS.primarygraycolor,
    fontFamily: FONTS.PoppinsRegular,
    fontSize: 13,
    marginTop: 40,
  },
});
export default Login;
