import React, { useState, useCallback, memo } from "react";
import {
  View,
  Text,
  TouchableOpacity,
  Image,
  StyleSheet,
  Modal,
  Platform,
  ViewStyle,
  TextStyle,
  ImageStyle,
  ImageSourcePropType,
} from "react-native";
import { COLORS, IMAGES } from "../../utills/Utills";
import Entypo from "react-native-vector-icons/Entypo";

type Option = {
  label: string;
  callback: (itemData?: any) => void;
  icon?: ImageSourcePropType;
  iconStyle?: ImageStyle;
  style?: ViewStyle;
  textStyle?: TextStyle;
};

type Props = {
  options: Option[];
  itemData?: any;
  iconStyle?: ImageStyle;
  buttonStyle?: ViewStyle;
  modalStyle?: ViewStyle;
  optionTextStyle?: TextStyle;
  cancelTextStyle?: TextStyle;
};

const ThreeDotsMenu: React.FC<Props> = memo(
  ({
    options = [],
    iconStyle = {},
    buttonStyle = {},
    modalStyle = {},
    optionTextStyle = {},
    cancelTextStyle = {},
    itemData = null,
  }) => {
    const [modalVisible, setModalVisible] = useState(false);

    const handleOptionPress = useCallback(
      (action: Option) => {
        setModalVisible(false);
        action?.callback?.(itemData);
      },
      [itemData]
    );

    const renderOptions = useCallback(() => {
      return options.map((option, index) => (
        <TouchableOpacity
          key={`option-${index}`}
          style={[styles.optionButton, option.style]}
          onPress={() => handleOptionPress(option)}
          activeOpacity={0.7}
        >
          {option.icon && (
            <Image
              source={option.icon}
              style={[styles.optionIcon, option.iconStyle]}
            />
          )}
          <Text style={[styles.optionText, optionTextStyle, option.textStyle]}>
            {option.label}
          </Text>
        </TouchableOpacity>
      ));
    }, [options, optionTextStyle]);

    return (
      <View style={styles.container}>
        <TouchableOpacity
          style={[styles.threeDotsButton, buttonStyle]}
          activeOpacity={0.5}
          onPress={() => setModalVisible(true)}
          hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
        >
          {/* <Image
            source={IMAGES.ThreeDots}
            style={[styles.threeDotsIcon, iconStyle]}
          /> */}
          <Entypo name={"dots-three-vertical"} color={COLORS.black} size={20} />
        </TouchableOpacity>

        <Modal
          animationType="fade"
          transparent={true}
          visible={modalVisible}
          onRequestClose={() => setModalVisible(false)}
        >
          <TouchableOpacity
            style={styles.modalOverlay}
            activeOpacity={1}
            onPress={() => setModalVisible(false)}
          >
            <View style={[styles.modalContent, modalStyle]}>
              {renderOptions()}
              <TouchableOpacity
                style={styles.cancelButton}
                onPress={() => setModalVisible(false)}
              >
                <Text style={[styles.cancelText, cancelTextStyle]}>Cancel</Text>
              </TouchableOpacity>
            </View>
          </TouchableOpacity>
        </Modal>
      </View>
    );
  }
);

const styles = StyleSheet.create({
  container: {
    zIndex: 1,
  },
  threeDotsButton: {
    paddingVertical: 10,
  },
  threeDotsIcon: {
    width: 20,
    height: 4,
    tintColor: "black",
  },
  modalOverlay: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "rgba(0, 0, 0, 0.5)",
  },
  modalContent: {
    width: Platform.select({ web: 250, default: "80%" } as any),
    backgroundColor: "white",
    borderRadius: 12,
    paddingVertical: 8,
    shadowColor: "#000",
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 5,
    gap: 10,
  },
  optionButton: {
    flexDirection: "row",
    alignItems: "center",
    // paddingVertical: 12,
    paddingHorizontal: 16,
  },
  optionIcon: {
    width: 20,
    height: 20,
    marginRight: 12,
    tintColor: "#333",
  },
  optionText: {
    fontSize: 16,
    color: "#333",
  },
  cancelButton: {
    paddingVertical: 12,
    paddingHorizontal: 16,
    borderTopWidth: 1,
    borderTopColor: "#f0f0f0",
    marginTop: 8,
  },
  cancelText: {
    fontSize: 16,
    color: "blue",
    textAlign: "center",
  },
});

export default ThreeDotsMenu;
