import AsyncStorage from "@react-native-async-storage/async-storage";
import axios from "axios";
import { Alert, Platform } from "react-native";

// Access_Token
export const getToken = async (): Promise<string | null> => {
  try {
    const token = await AsyncStorage.getItem("access_token");
    console.log("CHECK Token:", token);

    if (!token) {
      const message = "Access token not found. Please log in again.";

      if (Platform.OS === "web") {
        window.alert(`Alert\n\n${message}`);
      } else {
        Alert.alert("Error", message);
      }

      return null;
    }

    return token;
  } catch (error) {
    console.error("Error retrieving access token:", error);
    return null;
  }
};
export const getData = async (): Promise<string | null> => {
  try {
    const data = await AsyncStorage.getItem("mainProfile");

    return data;
  } catch (error) {
    console.error("Error mainProfile access token:", error);
    return null;
  }
};

// POST helper
export const POST = async (
  url: string,
  body?: any,
  token?: string
): Promise<any> => {
  try {
    const response = await axios.post(url, body, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        ...(token ? { Authorization: `Bearer ${token}` } : {}),
      },
    });

    console.log("response :>> ", response);
    return response;
  } catch (error: any) {
    console.error("POST API Error:", error?.response || error.message);
    console.log("error :>> ", error);
    throw error?.response?.data || error;
  }
};

// GET helper
export const GET = async (url: string, token?: string): Promise<any> => {
  try {
    const response = await axios.get(url, {
      headers: {
        "Content-Type": "application/json",
        ...(token ? { Authorization: `Bearer ${token}` } : {}),
      },
    });
    console.log("response get api :>> ", response);
    if (response.status === 204) {
      return response;
    }
    return response;
  } catch (error: any) {
    console.error("GET API Error:", error?.response || error.message);
    console.log("error get API  :>> ", error);
    throw error?.response?.data || error;
  }
};

