import React, { useState, useEffect } from "react";
import { View, Text, Pressable, Animated, StyleSheet } from "react-native";

const CustomToggle = ({ onToggle, initialValue = false }) => {
  const [isOn, setIsOn] = useState(initialValue);
  const toggleAnim = useState(new Animated.Value(initialValue ? 1 : 0))[0];

  useEffect(() => {
    if (onToggle) {
      onToggle(isOn);
    }
  }, [isOn]);

  const toggleSwitch = () => {
    Animated.timing(toggleAnim, {
      toValue: isOn ? 0 : 1,
      duration: 200,
      useNativeDriver: false,
    }).start();
    setIsOn((prev) => !prev);
  };

  const translateX = toggleAnim.interpolate({
    inputRange: [0, 1],
    outputRange: [2, 32],
  });

  return (
    <Pressable onPress={toggleSwitch}>
      <View
        style={[
          styles.toggleContainer,
          { backgroundColor: isOn ? "#4cd137" : "#dcdde1" },
        ]}
      >
        <Animated.View
          style={[styles.circle, { transform: [{ translateX }] }]}
        />
      </View>
    </Pressable>
  );
};

const styles = StyleSheet.create({
  toggleContainer: {
    width: 60,
    height: 30,
    borderRadius: 30,
    padding: 2,
    justifyContent: "center",
  },
  circle: {
    width: 26,
    height: 26,
    borderRadius: 13,
    backgroundColor: "#fff",
  },
  label: {
    marginTop: 8,
    fontSize: 16,
    textAlign: "center",
  },
});

export default CustomToggle;
