import React, { useRef, useState } from "react";
import {
  View,
  Image,
  Text,
  Dimensions,
  StyleSheet,
  TouchableOpacity,
  Modal,
} from "react-native";
import Carousel from "react-native-reanimated-carousel";
import { Video } from "expo-av"; // Only if you want to preview videos
import { Ionicons } from "@expo/vector-icons";
import { ENV } from "../api/api";
import { COLORS } from "./Utills";
import { BlurView } from "expo-blur";

const { width } = Dimensions.get("window");

interface MediaItem {
  files: string;
  media_type: string; // e.g., photo, video, pdf, etc.
  id?: number;
}

interface ImageCarouselProps {
  media: MediaItem[];
  showDotIndicator?: boolean;
}

const ImageCarousel: React.FC<ImageCarouselProps> = ({
  media,
  showDotIndicator = true,
}) => {
  const [activeIndex, setActiveIndex] = useState<number>(0);
  const carouselRef = useRef<any>(null);
  const [fullscreenVisible, setFullscreenVisible] = useState(false);
  const [selectedMedia, setSelectedMedia] = useState<MediaItem | null>(null);
  const renderMedia = (item: MediaItem) => {
    switch (item.media_type) {
      case "photo":
        return (
          <TouchableOpacity onPress={() => handlePress(item)}>
            <Image
              source={{ uri: `${ENV.BASE_URL}${item.files}` }}
              style={styles.image}
            />
          </TouchableOpacity>
        );
      case "video":
        return (
          <TouchableOpacity
            onPress={() => handlePress(item)}
            style={{
              width: "100%",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Video
              source={{ uri: `${ENV.BASE_URL}${item.files}` }}
              style={[
                styles.image,
                { width: 350, backgroundColor: "red", alignSelf: "center" },
              ]}
              isMuted
              shouldPlay={true}
            />
          </TouchableOpacity>
        );
      case "pdf":
      case "word":
      case "excel":
      case "ppt":
      case "text":
      case "link":
      case "podcast":
        return (
          <View style={[styles.image, styles.iconContainer]}>
            {/* <Ionicons
              name={getIconName(item.media_type)}
              size={50}
              color="#777"
            /> */}
            <Image source={getIconName(item.media_type)} style={styles.image} />
            {/* <Text numberOfLines={1} style={styles.iconLabel}>
              {item.media_type}
            </Text> */}
          </View>
        );
      default:
        return (
          <View style={[styles.image, styles.iconContainer]}>
            <Ionicons name="alert-circle-outline" size={50} color="#999" />
            <Text style={styles.iconLabel}>Unsupported</Text>
          </View>
        );
    }
  };

  const getIconName = (type: string) => {
    switch (type) {
      case "pdf":
        // return "document-text-outline";
        return require("../assets/images/pdf.png");
      case "word":
        return require("../assets/images/word-icon.png");
      case "excel":
        return require("../assets/images/excel-icon.png");
      case "ppt":
        return require("../assets/images/ppt-icon.png");
      case "text":
        return require("../assets/images/txt-icon.png");
      case "link":
        return require("../assets/images/link-icon.png");
      case "podcast":
        return require("../assets/images/audio-icon.png");
      default:
        return require("../assets/images/txt-icon.png");
    }
  };

  const handleDotPress = (index: number) => {
    setActiveIndex(index);
    carouselRef.current?.scrollTo?.({ index, animated: true });
  };
  const handlePress = (item) => {
    setSelectedMedia(item);
    setFullscreenVisible(true);
  };

  return (
    <View style={styles.container}>
      <Carousel
        ref={carouselRef}
        width={width - 40}
        height={200}
        data={media}
        autoPlay={true}
        autoPlayInterval={3000}
        loop
        snapEnabled={false}
        scrollAnimationDuration={800}
        onSnapToItem={setActiveIndex}
        renderItem={({ item }) => renderMedia(item)}
        panGestureHandlerProps={{
          enabled: media.length > 1, //  disables scroll if only 1 item
        }}
      />

      {showDotIndicator && (
        <View style={styles.dotsContainer}>
          {media.map((_, index) => (
            <TouchableOpacity
              key={index}
              onPress={() => handleDotPress(index)}
              style={[styles.dot, activeIndex === index && styles.activeDot]}
            />
          ))}
        </View>
      )}
      {fullscreenVisible && selectedMedia && (
        <Modal visible transparent>
          <BlurView
            intensity={70}
            style={styles.fullscreenContainer}
            tint="dark"
          >
            <TouchableOpacity
              style={styles.closeButton}
              onPress={() => setFullscreenVisible(false)}
            >
              <Ionicons name="close" size={30} color="#fff" />
            </TouchableOpacity>

            {selectedMedia.media_type === "photo" ? (
              <Image
                source={{ uri: `${ENV.BASE_URL}${selectedMedia.files}` }}
                style={styles.fullscreenImage}
              />
            ) : (
              <Video
                source={{ uri: `${ENV.BASE_URL}${selectedMedia.files}` }}
                style={styles.fullscreenImage}
                useNativeControls
                resizeMode="contain"
                shouldPlay
              />
            )}
          </BlurView>
        </Modal>
      )}
    </View>
  );
};

export default ImageCarousel;

const styles = StyleSheet.create({
  container: {
    alignItems: "center",
    marginTop: 10,
    // backgroundColor: COLORS.white,
    marginHorizontal: 20,
    borderRadius: 10,
    height: 300,
    justifyContent: "center",
  },
  image: {
    width: "100%",
    height: 200,
    borderRadius: 12,
    resizeMode: "contain",
    // backgroundColor: "#f0f0f0",
  },
  iconContainer: {
    justifyContent: "center",
    alignItems: "center",
    padding: 10,
  },
  iconLabel: {
    fontSize: 12,
    color: "#555",
    marginTop: 5,
    textAlign: "center",
  },
  dotsContainer: {
    flexDirection: "row",
    marginTop: 10,
  },
  dot: {
    width: 10,
    height: 10,
    borderRadius: 5,
    marginHorizontal: 5,
    backgroundColor: "#ccc",
  },
  activeDot: {
    backgroundColor: "#333",
  },
  fullscreenContainer: {
    flex: 1,
    backgroundColor: "black",
    justifyContent: "center",
    alignItems: "center",
  },
  fullscreenImage: {
    width: "80%",
    height: "80%",
    resizeMode: "contain",
    borderRadius: 10,
  },
  closeButton: {
    position: "absolute",
    top: 40,
    right: 20,
    zIndex: 1,
  },
});
