import {
  FlatList,
  ImageBackground,
  Platform,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
  useWindowDimensions,
} from "react-native";
import { useNavigation, useRoute } from "@react-navigation/native";
import { COLORS, IMAGES } from "../../utills/Utills";
import Banner from "../../components/Common/Banner";
import NavigationBar from "../../components/Common/NavigationBar";
import CustomInput from "../../utills/CustomInput";
import AntDesign from "react-native-vector-icons/AntDesign";
import Ionicons from "react-native-vector-icons/Ionicons";
import { DELETE, GET, POST, PUT, getToken } from "../../utills/Helper";
import { API } from "../../api/api";
import { useEffect, useState } from "react";
import { HoverTooltip } from "../../components/Common/HoverTooltip";
import { LinearGradient } from "expo-linear-gradient";

type QuestionList = {
  community: number;
  created_at: string;
  creator: string;
  description: string | null;
  id: number;
  is_resolved: boolean;
  likes_count: number;
  title: string;
  updated_at: string;
};

type QuestionListErr = {
  status: number;
  message?: string;
};

type AnswerList = {
  id: number;
  is_accepted: boolean;
  likes_count: number;
  request: number;
  responder: string;
  text: string;
  is_liked: boolean;
  is_responder: boolean;
};

const MyQuesAndAnswer = () => {
  const navigation = useNavigation<any>();
  const windowHeight = useWindowDimensions().height;

  const route = useRoute<any>();
  // const { community_id, my_community } = route.params;
  const community_id = 1;
  const my_community = false;

  const [questionList, setQuestionList] = useState<QuestionList[]>(null);
  const [questionListErr, setQuestionListErr] =
    useState<QuestionListErr | null>(null);
  const [answerList, setAnswerList] = useState<AnswerList[]>(null);
  const [quesAnsText, setQuesAnsText] = useState<string | null>(null);
  const [quesAnsTextErr, setQuesAnsTextErr] = useState<string | null>(null);
  const [showInput, setShowInput] = useState<boolean>(false);
  const [showAnsInput, setShowAnsInput] = useState<boolean>(false);
  const [isEditAnswer, setisEditAnswer] = useState<number | null>(null);

  const [isActiveQuestion, setiIsActiveQuestion] = useState<number | null>(
    null
  );

  const [isQuesCreator, setIsQuesCreator] = useState<boolean>(false);

  const handleGetQuestionList = async () => {
    try {
      const Token = await getToken();
      const URL = `${API.GET_COMMUNITY_REQUEST_QUESTION}${community_id}/`;

      const response = await GET(URL, Token);
      // console.log(" ~ handleGetQuestionList ~ response:", response);
      if (response.status === 204) {
        setQuestionList([response]);
      } else {
        setQuestionList(response?.data?.data);
      }
    } catch (error) {
      if (error.status === 403) {
        setQuestionListErr(error);
      }
    } finally {
    }
  };

  const handleGetAnswer = async (
    question_id: number,
    isCrateor: boolean,
    refresh: boolean = false
  ) => {
    setShowAnsInput(false);
    setQuesAnsText(null);
    setQuesAnsTextErr(null);

    if (refresh) {
    } else if (isActiveQuestion === question_id) {
      setiIsActiveQuestion(null);
      setIsQuesCreator(false);
      setAnswerList(null);
      return;
    }
    setiIsActiveQuestion(question_id);
    setIsQuesCreator(isCrateor);

    try {
      const Token = await getToken();
      const URL = `${API.GET_COMMUNITY_ANSWER}${question_id}/`;

      const response = await GET(URL, Token);

      if (response.status === 204) {
        setAnswerList(null);
      } else {
        setAnswerList(response?.data?.data);
      }
    } catch (error) {
      console.log(" ~ handleGetAnswer ~ error:", error);
    } finally {
    }
  };

  const handleGetMyCommunitiyQList = async () => {
    try {
      const Token = await getToken();
      const URL = API.GET_COMMUNITY_REQUEST_LIST;

      const response = await GET(URL, Token);
      if (response.status === 204) {
        setQuestionList([response]);
      } else {
        setQuestionList(response?.data?.data?.requests);
      }
    } catch (error) {
      console.log(" ~ handleGetCommQuesList ~ error:", error);
    } finally {
    }
  };

  const handleOnChange = (text) => {
    setQuesAnsText(text);
    setQuesAnsTextErr(null);
  };

  const handleCreateQues = async () => {
    if (quesAnsText == null || quesAnsText.trim() === "") {
      setQuesAnsTextErr("Required");
      return;
    }

    try {
      const Token = await getToken();
      const URL = `${API.CREATE_COMMUNITY_REQUEST_QUESTION}${community_id}/`;
      const data = { title: quesAnsText };

      const response = await POST(URL, data, Token);
      handleGetQuestionList();
      setShowInput(false);
      setQuesAnsText(null);
    } catch (error) {
      console.log(" ~ handlecreateQues ~ error:", error);
    } finally {
    }
  };

  const handleShowAnsInput = () => {
    setShowAnsInput(true);
    setShowInput(false);
    setQuesAnsText(null);
    setQuesAnsTextErr(null);
    setisEditAnswer(null);
  };

  const handleShowQuesInput = () => {
    setShowInput(true);
    setShowAnsInput(false);
    setQuesAnsText(null);
    setQuesAnsTextErr(null);
    setisEditAnswer(null);
  };

  const handleCreateAns = async (question_id: number) => {
    if (quesAnsText === null || quesAnsText.trim() === "") {
      setQuesAnsTextErr("Required");
      return;
    }

    try {
      const Token = await getToken();
      const URL = `${API.CREATE_COMMUNITY_ANSWER}${question_id}/`;
      const data = { text: quesAnsText.trim() };

      const response = await POST(URL, data, Token);
      handleGetAnswer(question_id, true, true);
      setShowAnsInput(false);
      setQuesAnsText(null);
    } catch (error) {
      console.log(" ~ handlecreateAns ~ error:", error);
    } finally {
    }
  };

  const showEditAnswer = (answer_id: number) => {
    setisEditAnswer(answer_id);
    setShowAnsInput(null);
    setQuesAnsText(null);
    setQuesAnsTextErr(null);
    setShowInput(false);
  };

  const handleEditAnswer = async (answer_id: number) => {
    if (quesAnsText == null || quesAnsText.trim() === "") {
      setQuesAnsTextErr("Required");
      return;
    }

    try {
      const Token = await getToken();
      const URL = `${API.EDIT_COMMUNITY_ANSWER}${answer_id}/`;
      const getURL = `${API.GET_EDITED_COMMUNITY_ANSWER}${answer_id}/`;

      const data = { text: quesAnsText.trim() };
      const response = await PUT(URL, data, Token);
      if (response.status === 200) {
        handleUpdateAnswerList(answer_id);
      }
      setisEditAnswer(null);
      setQuesAnsText(null);
      setQuesAnsTextErr(null);
    } catch (error) {
      console.log(" ~ showEditAnswer ~ error:", error);
    } finally {
    }
  };

  const handleUpdateAnswerList = async (answer_id: number) => {
    console.log("answer_id update answer list :>> ", answer_id);
    try {
      const Token = await getToken();
      const URL = `${API.GET_EDITED_COMMUNITY_ANSWER}${answer_id}/`;
      const getResponse = await GET(URL, Token);
      console.log(" ~ handleUpdateAnswerList ~ getResponse:", getResponse);
      const updatedAnswerList = answerList.map((item) =>
        item.id === getResponse.data.data.id ? getResponse.data.data : item
      );
      setAnswerList(updatedAnswerList);
    } catch (error) {
      console.log(" ~ handleUpdateAnswerList ~ error:", error);
    }
  };

  const handleDeleteAnswer = async (answer_id: number) => {
    const confirmed = window.confirm("Are you sure you want to delete answer?");
    try {
      if (confirmed) {
        const Token = await getToken();
        const URL = `${API.DELETE_COMMUNITY_ANSWER}${answer_id}/`;

        const upddateList = answerList?.filter((del) => del.id !== answer_id);
        setAnswerList(upddateList.length > 0 ? upddateList : null);

        const response = await DELETE(URL, Token);
      } else {
        console.log("Cancelled");
      }
    } catch (error) {
      console.log(" ~ handleDeleteAnswer ~ error:", error);
    }
  };

  const handleLikeAns = async (answer_id: number, answer_isLiked: boolean) => {
    try {
      const Token = await getToken();
      const URL = `${API.LIKE_UNLIKE_ANSWER}${answer_id}/`;
      const data = {
        action: answer_isLiked ? "unlike" : "like",
        obj_type: "answer",
      };

      const response = await POST(URL, data, Token);

      const upddateList = answerList?.map((res) => {
        if (res.id === answer_id) {
          return {
            ...res,
            is_liked: !answer_isLiked,
          };
        }
        return res;
      });
      setAnswerList(upddateList);
    } catch (error) {
      console.log(" ~ handleLikeeAns ~ error:", error);
    }
  };

  const handleSolveAnswer = async (answer_id: number) => {
    try {
      const Token = await getToken();
      const URL = `${API.MARK_ANS_AS_SOLVED + answer_id}/`;
      const data = {};

      const response = await POST(URL, data, Token);
      handleGetAnswer(isActiveQuestion, true, true);
    } catch (error) {
      console.log(" ~ handleSolveAnswer ~ error:", error);
      if (
        error.status === 400 &&
        error.message === "Answer is already accepted."
      ) {
        if (Platform.OS == "web") {
          window.alert(error.message);
        }
      }
    }
  };

  useEffect(() => {
    if (community_id) {
      handleGetQuestionList();
    } else if (my_community) {
      handleGetMyCommunitiyQList();
    }
  }, [community_id, my_community]);

  const renderItem = ({ item }) => {
    // console.log(" ~ renderItem ~ item:", item);
    if (item.status === 204) {
      return (
        <View style={styles.notFound}>
          <Text style={[styles.ansText, { textAlign: "center" }]}>
            No questions have been asked yet.
          </Text>
        </View>
      );
    }

    return (
      <View>
        <View style={styles.quesContainer}>
          <View style={styles.ques}>
            <Text style={styles.quesText}>{item.title}</Text>
            {item.description && (
              <Text style={styles.quesDescription}>{item.description}</Text>
            )}
          </View>

          <TouchableOpacity
            style={styles.questionExpand}
            onPress={() => {
              handleGetAnswer(item.id, item.is_creator);
            }}
          >
            <AntDesign
              name={isActiveQuestion === item.id ? "minus" : "plus"}
              size={24}
            />
          </TouchableOpacity>
        </View>

        {isActiveQuestion == item.id && (
          <View style={styles.answerBox}>
            {answerList !== null ? (
              answerList?.map((answer, index) => {
                // console.log(" ~ answerList?.map ~ answer:", answer);
                if (answer.request == item.id)
                  return (
                    <View
                      style={[
                        styles.answerStyle,
                        { borderTopWidth: index > 0 ? 1 : 0 },
                      ]}
                      key={index}
                    >
                      <View
                        style={{
                          flexDirection: "row",
                          gap: 20,
                          alignItems: "center",
                        }}
                      >
                        {answer.is_accepted && (
                          <HoverTooltip
                            tooltipText="Accepted by author"
                            position="right"
                          >
                            <Ionicons
                              name={
                                answer.is_accepted && "checkmark-done-sharp"
                              }
                              size={16}
                              color={COLORS.white}
                            />
                          </HoverTooltip>
                        )}

                        <View style={{ zIndex: -100 }}>
                          <Text style={styles.ansResponder}>
                            {answer.responder}
                          </Text>
                          <Text style={styles.ansText}>{answer.text}</Text>
                        </View>
                      </View>

                      {isEditAnswer === answer.id ? (
                        <View
                          style={[styles.inputContainer, { paddingTop: 5 }]}
                        >
                          <View style={styles.inputBox}>
                            <CustomInput
                              value={quesAnsText ?? ""}
                              onChangeText={(text) => handleOnChange(text)}
                              placeholder={"Edit Your Answer"}
                              inputStyle={styles.inputStyle}
                              errorMessage={quesAnsTextErr}
                            />
                          </View>
                          <TouchableOpacity
                            style={styles.sendBtn}
                            onPress={() => handleEditAnswer(answer.id)}
                          >
                            <Text style={styles.sendTxt}>Submit</Text>
                          </TouchableOpacity>
                        </View>
                      ) : (
                        <View style={styles.ansBottombar}>
                          <HoverTooltip
                            tooltipText={answer.is_liked ? "Unlike" : "Like"}
                          >
                            <TouchableOpacity
                              onPress={() =>
                                handleLikeAns(answer.id, answer.is_liked)
                              }
                            >
                              <AntDesign
                                name={answer.is_liked ? "like1" : "like2"}
                                size={16}
                                color={
                                  answer.is_liked ? COLORS.blue : COLORS.white
                                }
                              />
                            </TouchableOpacity>
                          </HoverTooltip>
                          <HoverTooltip tooltipText="Edit">
                            <TouchableOpacity
                              onPress={() => showEditAnswer(answer.id)}
                            >
                              <AntDesign
                                name={"edit"}
                                size={16}
                                color={COLORS.white}
                              />
                            </TouchableOpacity>
                          </HoverTooltip>
                          <HoverTooltip tooltipText="Delete">
                            <TouchableOpacity
                              onPress={() => handleDeleteAnswer(answer.id)}
                            >
                              <AntDesign
                                name={"delete"}
                                size={16}
                                color={COLORS.white}
                              />
                            </TouchableOpacity>
                          </HoverTooltip>

                          {isQuesCreator && (
                            <HoverTooltip tooltipText="Mark as Solve">
                              <TouchableOpacity
                                onPress={() => handleSolveAnswer(answer.id)}
                              >
                                <AntDesign
                                  name={"check"}
                                  size={16}
                                  color={COLORS.white}
                                />
                              </TouchableOpacity>
                            </HoverTooltip>
                          )}
                        </View>
                      )}
                    </View>
                  );
              })
            ) : (
              <View style={[styles.answerStyle]}>
                <Text style={[styles.ansResponder, { textAlign: "center" }]}>
                  oh! Answer is not availble yet.
                </Text>
              </View>
            )}

            {showAnsInput ? (
              <View style={styles.inputContainer}>
                <View style={styles.inputBox}>
                  <CustomInput
                    value={quesAnsText ?? ""}
                    onChangeText={(text) => handleOnChange(text)}
                    placeholder={"Your Valuable Answer"}
                    inputStyle={styles.inputStyle}
                    errorMessage={quesAnsTextErr}
                  />
                </View>
                <TouchableOpacity
                  style={styles.sendBtn}
                  onPress={() => handleCreateAns(item.id)}
                >
                  <Text style={styles.sendTxt}>Submit</Text>
                </TouchableOpacity>
              </View>
            ) : (
              <TouchableOpacity
                style={styles.ansTxt}
                activeOpacity={0.8}
                onPress={() => handleShowAnsInput()}
              >
                <Text style={styles.ansBtnText}>Post Your Answer</Text>
              </TouchableOpacity>
            )}
          </View>
        )}
      </View>
    );
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={styles.scrollableContentContainer}
      >
        <Banner backNavigation />
        <NavigationBar />

        <View
          style={[
            styles.bodyContentContainer,
            { minHeight: windowHeight - 360 },
          ]}
        >
          <View style={styles.quesHeading}>
            <Text style={styles.heading}>Qustion Answer</Text>
            {community_id && questionListErr == null && (
              <TouchableOpacity
                style={styles.quesBtn}
                onPress={() => handleShowQuesInput()}
              >
                <Text style={styles.quesText}>Ask!</Text>
              </TouchableOpacity>
            )}
          </View>

          {questionListErr?.status == 403 && (
            <View style={{ alignItems: "center" }}>
              <Text style={[styles.ansText, { textAlign: "center" }]}>
                {questionListErr?.message}
              </Text>
            </View>
          )}

          {showInput && (
            <View style={styles.inputContainer}>
              <View style={styles.inputBox}>
                <CustomInput
                  value={quesAnsText ?? ""}
                  onChangeText={(text) => handleOnChange(text)}
                  placeholder={"What are the details of your problem?"}
                  inputStyle={styles.inputStyle}
                  errorMessage={quesAnsTextErr}
                />
              </View>
              <TouchableOpacity
                style={styles.sendBtn}
                onPress={() => handleCreateQues()}
              >
                <Text style={styles.sendTxt}> Send</Text>
              </TouchableOpacity>
            </View>
          )}

          <View style={styles.contentContainer}>
            <FlatList
              data={questionList}
              renderItem={renderItem}
              keyExtractor={(item, index) => index.toString()}
              contentContainerStyle={{ paddingBottom: 20, gap: 20 }} // Optional
              showsVerticalScrollIndicator={false}
            />
          </View>

          {/* <View style={styles.inputContainer}>
            <CustomInput
              //   value={userDetails.last_name}
              //   onChangeText={(text) => handleUserChange("last_name", text)}
              placeholder={"Enter Your Name"}
              maxLength={30}
              numOfColumn={3}
              //   label={"Last Name"}
              //   inputStyle={styles.inputStyle}
            />
          </View> */}
        </View>
      </ScrollView>
    </ImageBackground>
  );
};

export default MyQuesAndAnswer;

