import { useNavigation } from "@react-navigation/native";
import React from "react";
import {
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  useWindowDimensions,
} from "react-native";

const navigationtabs = ["NewsScreen", "ProfileScreen", "ExchangeScreen"];

const NavigationBar = () => {
  const navigation = useNavigation<any>();

  const state = navigation.getState();
  const currentRoute = state.routeNames[0];

  const windowWidth = useWindowDimensions().width;
  const isSmallScreen = windowWidth < 768;
  const isMobileScreen = windowWidth < 426;
  const containerWidth = isMobileScreen ? "90%" : isSmallScreen ? "80%" : "60%";

  return (
    <View style={[styles.container, { width: containerWidth }]}>
      {navigationtabs.map((tab) => (
        <TouchableOpacity
          key={tab}
          style={[
            styles.tabButton,
            currentRoute === tab.replace("Screen", "") &&
              styles.selectedTabButton,
          ]}
          onPress={() => navigation.navigate(tab)}
        >
          <Text
            style={[
              styles.tabText,
              currentRoute === tab.replace("Screen", "") &&
                styles.selectedTabText,
            ]}
          >
            {tab.replace("Screen", "")}
          </Text>
        </TouchableOpacity>
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: "white",
    height: 55,
    borderBottomEndRadius: 40,
    borderBottomStartRadius: 40,
    minWidth: 240,
    flexDirection: "row",
    justifyContent: "space-around",
    padding: 2,
  },
  tabButton: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
    paddingVertical: 10,
    borderBottomEndRadius: 40,
    borderBottomStartRadius: 40,
  },
  selectedTabButton: {
    backgroundColor: "#2D2B9B",
  },
  tabText: {
    fontSize: 16,
    fontWeight: "bold",
    color: "#000",
  },
  selectedTabText: {
    color: "white",
  },
});

export default NavigationBar;
