import React, { useState } from "react";
import {
  View,
  Text,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  Image,
  useWindowDimensions,
} from "react-native";
import { COLORS, IMAGES } from "../utills/Utills";

interface SearchBoxProps {
  placeholder?: string;
  onSearch?: (query: string) => void;
}

const SearchBox: React.FC<SearchBoxProps> = ({
  placeholder = "Search box",
  onSearch,
}) => {
  const [searchText, setSearchText] = useState("");
  const { width: windowWidth } = useWindowDimensions();
  const isSmallScreen = windowWidth < 800;

  const handleSearch = () => {
    if (onSearch) {
      onSearch(searchText);
      setSearchText("");
    }
  };

  return (
    <View style={[styles.container, isSmallScreen && styles.smallContainer]}>
      <Image source={IMAGES.search} style={styles.icon} />
      <TextInput
        value={searchText}
        onChangeText={setSearchText}
        placeholder={placeholder}
        placeholderTextColor={COLORS.white}
        style={styles.input}
      />
      <TouchableOpacity onPress={handleSearch} style={styles.searchButton}>
        <Text style={styles.searchText}>Search</Text>
      </TouchableOpacity>
    </View>
  );
};

export default SearchBox;

const styles = StyleSheet.create({
  container: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    marginVertical: 14,
    borderRadius: 16,
    borderWidth: 1,
    borderColor: COLORS.white,
    width: "44%",
    minWidth: 100,
    alignSelf: "center",
    padding: 10,
    gap: 10,
  },
  smallContainer: {
    width: "90%",
  },
  icon: {
    height: 17.5,
    width: 17.5,
    resizeMode: "contain",
  },
  input: {
    flex: 1,
    height: 44,
    color: COLORS.white,
    borderRadius: 16,
    fontSize: 14,
    fontFamily: "PoppinsRegular",
    outlineStyle: "none",
    minWidth: 100,
  },
  searchButton: {
    backgroundColor: COLORS.white,
    height: 35,
    paddingHorizontal: 15,
    borderRadius: 10,
    alignItems: "center",
    justifyContent: "center",
  },
  searchText: {
    fontSize: 14,
    fontFamily: "PoppinsRegular",
    color: COLORS.blue,
    textAlign: "center",
  },
});
