import {
  ImageBackground,
  Platform,
  ScrollView,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import { useNavigation, useRoute } from "@react-navigation/native";
import { COLORS, IMAGES } from "../../utills/Utills";
import Banner from "../../components/Common/Banner";
import NavigationBar from "../../components/Common/NavigationBar";
import * as ImagePicker from "expo-image-picker";
import { useState } from "react";
import AntDesign from "react-native-vector-icons/AntDesign";
import { getToken } from "../../utills/Helper";
import { API } from "../../api/api";
import axios from "axios";
import { LinearGradient } from "expo-linear-gradient";

const CommunityBgImage = () => {
  const [images, setImages] = useState<any | null>(null);
  console.log(" ~ CommunityBgImage ~ images:", images);

  const navigation = useNavigation<any>();

  const route = useRoute<any>();
  const { community_id, backgroundImage } = route.params;
  //   const community_id = 1;
  console.log(" ~ CommunityBgImage ~ backgroundImage:", backgroundImage);
  console.log("community_id 6665565656565656:>> ", community_id);

  const handleChooseImage = async () => {
    // setModalVisible(true);
    // Request permission for the image picker
    const permissionResult =
      await ImagePicker.requestMediaLibraryPermissionsAsync();
    if (permissionResult.granted === false) {
      alert("Permission to access media library is required!");
      return;
    }
    // Launch image picker for multiple images
    const result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ["images"],
      allowsMultipleSelection: false, // Allows multiple selection
      quality: 1,
      base64: true,
    });

    if (!result.canceled) {
      console.log("result===>>>", result);

      const selectedImages = result.assets[0];
      console.log(" ~ handleChooseImage ~ selectedImages:", selectedImages);
      setImages(selectedImages);
    }
  };

  const handleUploadImage = async () => {
    console.log("upload images 5555");
    const Token = await getToken();
    const URL = `${API.UPDATE_COMMUNITY_BACKGROUND_IMAGE + community_id}/`;

    try {
      const formData = new FormData();
      //   if (Platform.OS === "web") {
      //     console.log(" ~ handleUploadImage ~ images:", images);
      //     const response = await fetch(images.uri);
      //     const blob = await response.blob();
      //     console.log(" ~ handleUploadImage ~ response:", response);
      //     console.log(" ~ handleUploadImage ~ blob:", blob);
      //     console.log(" ~ handleUploadImage ~ images.name:", images.fileName);
      //     formData.append("background_image", blob, images.fileName);
      //   }

      if (Platform.OS === "web") {
        // Web-specific handling
        const response = await fetch(images.uri);
        const blob = await response.blob();

        // Create a filename if not available
        const fileName = images.fileName || `image-${Date.now()}.jpg`;

        formData.append("background_image", blob, fileName);
      }

      const config = {
        method: "put",
        url: URL,
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${Token}`,
          "Content-Type": "multipart/form-data",
        },
        data: formData,
      };

      const response = await axios.request(config);
      console.log("Api Responce Post data", response.data);

      if (response.data) {
        console.log("Community back ground image successfully uploaded");
        navigation.goBack();
      }
    } catch (error) {
      console.error(
        "Post Error:",
        error.response ? error.response.data : error.message
      );
    } finally {
      //   setIsLoading(false);
    }
  };

  const handleRemoveImage = () => {
    if (Platform.OS === "web") {
      const confirm = window.confirm("Are you sure to remove image");
      if (confirm) {
        setImages(null);
      }
    }
  };

  return (
    <ImageBackground style={styles.backGroundImg}>
      <LinearGradient
        colors={["#130f4b", "#130f4b", "#523398", "#6333a8"]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 1 }}
        style={StyleSheet.absoluteFill}
      />
      {/* <ScrollView
        keyboardShouldPersistTaps="handled"
        style={styles.scrollableContent}
        showsVerticalScrollIndicator={false}
        contentContainerStyle={styles.scrollableContentContainer}
      > */}
      <Banner backNavigation />
      <NavigationBar />
      <ImageBackground
        style={styles.container}
        source={
          images !== null
            ? { uri: images?.uri }
            : !!backgroundImage
            ? { uri: backgroundImage }
            : IMAGES.CommbackImg
        }
      >
        <View style={styles.innerContainer}>
          <Text style={styles.contentTxt}>
            Change Community background Image
          </Text>
          <TouchableOpacity
            style={styles.btn}
            onPress={() => handleChooseImage()}
          >
            <Text style={styles.btnTxt}>Select Image</Text>
          </TouchableOpacity>
          {images && (
            <View
              style={{ flexDirection: "row", alignItems: "center", gap: 5 }}
            >
              <TouchableOpacity
                style={styles.btn}
                onPress={() => handleUploadImage()}
              >
                <Text style={styles.btnTxt}>Upload</Text>
              </TouchableOpacity>

              <TouchableOpacity
                onPress={() => {
                  handleRemoveImage();
                }}
              >
                <AntDesign name={"delete"} color={COLORS.black} size={30} />
              </TouchableOpacity>
            </View>
          )}
        </View>
      </ImageBackground>
      {/* </ScrollView> */}
    </ImageBackground>
  );
};

export default CommunityBgImage;

const styles = StyleSheet.create({
  backGroundImg: {
    flex: 1,
    alignItems: "center",
    paddingHorizontal: 20,
  },
  //   scrollableContent: {
  //     flex: 1,
  //     paddingVertical: 20,
  //   },
  //   scrollableContentContainer: {
  //     alignItems: "center",
  //   },

  container: {
    flex: 1,
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    marginVertical: 20,
    padding: 50,
    overflow: "hidden",
    borderRadius: 20,
  },
  innerContainer: {
    backgroundColor: "rgba(255, 255, 255, 0.9)",
    // flex: 1,
    padding: 50,

    alignItems: "center",
    justifyContent: "center",
    borderRadius: 8,
    borderWidth: 1,
    borderColor: COLORS.white,
    gap: 15,
  },

  contentTxt: {
    color: COLORS.black,
    // backgroundColor: "red",
  },

  btn: {
    backgroundColor: "blue",
    padding: 10,
    borderWidth: 1,
    borderColor: COLORS.white,
    borderRadius: 20,
    paddingHorizontal: 30,
  },
  btnTxt: {
    color: COLORS.white,
  },
});
