import React from "react";
import {
  Text,
  StyleSheet,
  TouchableOpacity,
  Image,
  useWindowDimensions,
} from "react-native";
import { COLORS, IMAGES } from "../../utills/Utills";
import { useNavigation } from "@react-navigation/native";

type CommanHeader = {
  backNavigation?: boolean;
};

const Banner: React.FC<CommanHeader> = ({ backNavigation = false }) => {
  const navigation = useNavigation();
  const windowWidth = useWindowDimensions().width;
  const isSmallScreen = windowWidth < 800;
  const isMobileScreen = windowWidth < 426;
  return (
    <TouchableOpacity
      onPress={() => backNavigation && navigation.goBack()}
      activeOpacity={backNavigation ? 0.8 : 1}
      style={[styles.container, { height: isMobileScreen ? 140 : 240 }]}
    >
      <Image source={IMAGES.Banner} style={styles.img} />
      <Text style={[styles.headerText, { fontSize: isMobileScreen ? 24 : 32 }]}>
        Swiss X-Change
      </Text>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  container: {
    width: "100%",
    borderWidth: 1,
    borderColor: COLORS.white,
    borderRadius: 16,
    alignItems: "center",
    alignSelf: "center",
  },
  img: {
    height: "100%",
    width: "100%",
    borderRadius: 16,
    alignSelf: "center",
  },
  headerText: {
    color: COLORS.white,
    fontFamily: "PoppinsSemi",
    position: "absolute",
    margin: 20,
    left: 0,
    fontSize: 32,
  },
});

export default Banner;
