import React, { ReactNode } from "react";
import {
  View,
  Text,
  StyleSheet,
  Pressable,
  Platform,
  ViewStyle,
} from "react-native";

type HoverTooltipProps = {
  children: ReactNode;
  tooltipText: string;
  position?: "top" | "bottom" | "left" | "right";
};

export const HoverTooltip = ({
  children,
  tooltipText,
  position = "top",
}: HoverTooltipProps) => {
  const [isVisible, setIsVisible] = React.useState(false);

  // Only show hover effects on web
  const isWeb = Platform.OS === "web";

  const handleHover = (state: boolean) => {
    if (isWeb) {
      setIsVisible(state);
    }
  };

  const getTooltipPosition = (): ViewStyle => {
    const commonStyles: ViewStyle = {
      alignSelf: "center",
    };

    switch (position) {
      case "bottom":
        return {
          ...commonStyles,
          top: "100%" as unknown as number, // Type workaround for web
          marginTop: 4,
        };
      case "left":
        return {
          ...commonStyles,
          right: "100%" as unknown as number,
          marginRight: 4,
          top: -10,
        };
      case "right":
        return {
          ...commonStyles,
          left: "100%" as unknown as number,
          marginLeft: 4,
          top: -10,
        };
      default: // top
        return {
          ...commonStyles,
          bottom: "100%" as unknown as number,
          marginBottom: 4,
        };
    }
  };

  return (
    <View style={styles.hoverContainer}>
      <Pressable
        onHoverIn={() => handleHover(true)}
        onHoverOut={() => handleHover(false)}
        onPress={() => (isWeb ? null : setIsVisible(!isVisible))}
        style={({ pressed }) => [
          styles.targetElement,
          pressed && !isWeb && styles.pressed,
        ]}
      >
        {children}
      </Pressable>

      {isVisible && (
        <View style={[styles.tooltip, getTooltipPosition()]}>
          <Text style={styles.tooltipText}>{tooltipText}</Text>
        </View>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  hoverContainer: {
    position: "relative",
  },
  targetElement: {
    // padding: 8,
  },
  pressed: {
    opacity: 0.7,
  },
  tooltip: {
    position: "absolute",
    backgroundColor: "#333",
    paddingHorizontal: 8,
    paddingVertical: 4,
    borderRadius: 4,
    zIndex: 100,
    alignSelf: "center",
    flex: 1,
  },
  tooltipText: {
    color: "white",
    fontSize: 12,
  },
});
